/**
	 * 联查
	 * @param {*} val 
	 */
import Utils from '../../../../public/utils';
import { toast } from 'nc-lightapp-front';
const { splitURI, langCheck, setGlobalStorage } = Utils;
export default function relationSearch(key) {
	let iconDOM = document.getElementById('liancha');
	if (iconDOM) {
		iconDOM.classList.remove('icon-hangcaozuoxiangshang1');
		iconDOM.classList.remove('hangcaozuoxiangshang1');
		iconDOM.classList.add('icon-hangcaozuoxiala1');
		iconDOM.classList.add('hangcaozuoxiala1');
		this.cellPerch = false;
	}

	if (!this.sortAndFilterArea) {
		toast({ content: langCheck('reportMultiLang', '100301-000016'), color: 'warning' }); /* 国际化处理： 请在有效数据区域内进行联查*/
		return;
	}
	let { operationQueue, pager, settings } = this.state;
	const { condition } = this.pageParams;
	let { uSearchMenuArr } = this.initParams;
	// key 即json片段中的targetid，根据targetid取需要的参数
	const coords = this.coords;
	const row = coords.row.toString();
	const column = coords.col > -1 ? coords.col.toString() : '0';
	let targetRepPk, targetid, targetcode, url, targetname, targetpage, webDrillType, targetPath, drillCodeKey, fldName, pk_storyboard, analysis_params, graphicUrlFlag = false;
	if (key.key) {
		drillCodeKey = key.key;
		uSearchMenuArr.forEach((item) => {
			if (item.drillCode == key.key) {
				targetid = item.targetid;
				targetcode = item.targetcode;
				targetname = item.targetname;
				targetpage = item.targetpage;
				targetRepPk = item.targetRepPk;
				webDrillType = item.webDrillType;
				targetPath = item.targetPath;
				fldName = item.fldName;
				pk_storyboard = item.targetStoryBoardPk || '';
				analysis_params = item.analysisParams || {};
				if ((item.webDrillType == 3 || item.webDrillType == 4) && !item.targetPath) graphicUrlFlag = true;
			}
		});
	} else {
		drillCodeKey = key;
		uSearchMenuArr.forEach((item) => {
			if (item.drillCode == key) {
				targetid = item.targetid;
				targetcode = item.targetcode;
				targetname = item.targetname;
				targetpage = item.targetpage;
				targetRepPk = item.targetRepPk;
				webDrillType = item.webDrillType;
				targetPath = item.targetPath;
				fldName = item.fldName;
				pk_storyboard = item.targetStoryBoardPk || '';
				analysis_params = item.analysisParams || {};
				if ((item.webDrillType == 3 || item.webDrillType == 4) && !item.targetPath) graphicUrlFlag = true;
			}
		});
	}
	url = window.location.pathname;
	targetPath = splitURI(targetPath) ? splitURI(targetPath) : splitURI(url);
	if (graphicUrlFlag) targetPath = "/nccloud/resources/graphic_report/graphicReportDesign/pages/busi/index.html"
	setGlobalStorage('sessionStorage', `condition${this.commonParams.reportId}*${drillCodeKey}`, JSON.stringify(condition));
	setGlobalStorage('sessionStorage', `trans${this.commonParams.opents}${this.commonParams.reportId}`, this.commonParams.transSaveObject || '')
	let drillUrlObj = {
		ar: targetid,
		n: targetname,
		appcode: targetcode,
		pageCode: targetpage,
		row: row,
		column: column,
		opents: this.commonParams.opents,
		targetRepPk: targetRepPk,
		reportId: this.commonParams.reportId,
		drillCode: drillCodeKey,
		webDrillType: webDrillType.toString(),
		oid: this.commonParams.oid,
		operationQueue,
		pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
		pageSize: (pager.pageSize && pager.pageSize.toString()) || '',
		pk_storyboard,
		analysis_params: JSON.stringify(analysis_params)
	};
	if (this.props.setConnectionSearch) {
		let allData = {
			row,
			column,
			targetRepPk,
			dirllcode: drillCodeKey,
			fldName
		};
		let searchData = this.sendSearchParams; // 如果联查报表后没有查询直接联查单据时
		if (!this.isClickSearchBtn) {
			let others = {
				operationQueue,
				pageInfo: {
					pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
					pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
				},
				querycondition: condition,
				isAdvanced: true,
				key_time_offset: this.key_time_offset,
				userdefObj: [],
				custcondition: {},
				requireTotalData: true
			};
			searchData = Object.assign({}, this.commonParams, others);
		} else {
			searchData = Object.assign({}, searchData, { transSaveObject: this.commonParams.transSaveObject });
		}
		allData = Object.assign({}, allData, searchData);
		let itemObj = key.key ? { key: key.key } : { key: key };
		let isCheckedRow = settings.data[coords.row];
		this.props.setConnectionSearch(
			this.commonParams.transSaveObject,
			itemObj,
			allData,
			this.props,
			targetPath,
			drillUrlObj,
			`trans${this.commonParams.opents}${this.commonParams.reportId}`,
			isCheckedRow
		);
	} else {
		this.props.openTo(targetPath, drillUrlObj);
	}
}
