import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCModal, NCButton } = base;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class ConfirmModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showConfirmModal: false
		};
	}

	closeConfirmModal() {
		this.setState({
			showConfirmModal: false
		});
	}

	render() {
		return (
			<NCModal
				show={this.state.showConfirmModal}
				id="confirmModal"
				backdrop={false}
				className="report-smallmodal-class"
			>
				<NCModal.Header />
				<NCModal.Body>{langCheck('reportMultiLang', '100301-000046')}</NCModal.Body>{/* 国际化处理： 当前数据较多，系统在处理完成后会发送通知消息，是否继续？*/}
				<NCModal.Footer>
					<NCButton colors="primary" className="sure-button" onClick={this.props.sureConfirmModal}>
						{langCheck('reportMultiLang', '100301-000047')}{/* 国际化处理： 继续*/}
					</NCButton>
					<NCButton className="cancel-button" onClick={this.closeConfirmModal.bind(this)}>
						{langCheck('reportMultiLang', '100301-000048')}{/* 国际化处理： 取消*/}
					</NCButton>
				</NCModal.Footer>
			</NCModal>
		);
		}
}
