import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
import UseridsRefer from '../../../public/uap/index.js';
const { NCModal, NCButton, NCRadioGroup, NCTextArea, NCFormControl, NCRadio } = base;
import Utils from '../utils';
const { langCheck } = Utils;
require('../common/index.less');
require('./index.less');

export default class ShareModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showShareModal: false,
			title: props.data.getUrlParam('n') || props.data.getSearchParam('n') || '', //消息标题
			sendmethod: '0', //附件类型
			content: '', //消息正文
			userids: null //接收人
		};
	}

	closeShareModal() {
		this.setState({
			showShareModal: false,
			sendmethod: '0', //附件类型
			content: '', //消息正文
			userids: null //接收人
		});
	}

	onParamChange(way, e) {
		this.setState({
			[way]: e
		});
	}

	render() {
		const { title, sendmethod, content, userids } = this.state;
		return (
			<NCModal show={this.state.showShareModal} id="shareModal" className="report-modal-class">
				<NCModal.Header>
					<NCModal.Title fieldid={langCheck('reportMultiLang', '100301-000078')}>{langCheck('reportMultiLang', '100301-000078')}</NCModal.Title>
					{/* 国际化处理： 报表订阅*/}
				</NCModal.Header>
				<NCModal.Body>
					<div className="user-info">
						<span className="left">{langCheck('reportMultiLang', '100301-000071')}</span>
						{/* 国际化处理： 消息标题*/}
						<div className="right">
							<NCFormControl value={title} onChange={this.onParamChange.bind(this, 'title')} />
						</div>
					</div>
					<div className="user-info">
						<span className="left">{langCheck('reportMultiLang', '100301-000079')}</span>
						{/* 国际化处理： 接收人*/}
						<div className="right">
							<UseridsRefer
								value={userids}
								onChange={(value) => {
									this.setState({
										userids: value
									});
								}}
								queryCondition={{
									isAllUserVisible: true,
									showSysAdmin: 'true',
									showGroupAdmin: 'true'
								}}
							/>
						</div>
					</div>
					<div className="user-info">
						<span className="left">{langCheck('reportMultiLang', '100301-000080')}</span>
						{/* 国际化处理： 附件类型*/}
						<div className="right" style={{ width: 'auto' }}>
							<NCRadio.NCRadioGroup
								selectedValue={sendmethod}
								onChange={this.onParamChange.bind(this, 'sendmethod')}
							>
								<NCRadio value="0">{'EXCEL'}</NCRadio>
								<NCRadio value="1">{'PDF'}</NCRadio>
								<NCRadio value="2">{'CSV'}</NCRadio>
							</NCRadio.NCRadioGroup>
						</div>
					</div>
					<div className="user-info">
						<span className="left">{langCheck('reportMultiLang', '100301-000081')}</span>
						{/* 国际化处理： 消息正文*/}
						<div className="right" style={{ width: 354 }}>
							<NCTextArea value={content} onChange={this.onParamChange.bind(this, 'content')} />
						</div>
					</div>
				</NCModal.Body>
				<NCModal.Footer>
					<NCButton colors="primary" className="sure-button" onClick={this.props.shareReport.bind(this, this.state)}>
						{langCheck('reportMultiLang', '100301-000039')}
						{/* 国际化处理： 分享*/}
					</NCButton>
					<NCButton className="cancel-button" onClick={this.closeShareModal.bind(this)}>
						{langCheck('reportMultiLang', '100301-000048')}
					</NCButton>
					{/* 国际化处理： 取消*/}
				</NCModal.Footer>
			</NCModal>
		);
	}
}
