import React, { Component } from 'react';
import { HotTable } from '@handsontable/react';
require('handsontable/dist/handsontable.full.css');
require('./index.less');

export default class Handsontable extends Component {
	constructor(props) {
		super(props);
		this.state = {
			coords: { row: null, col: null }, // 选中单元格坐标
			colWidths: [],
			rowHeights: [],
			isCheckedInTable: false
		};
	}

	returnData() {
		// 返回选中单元格x y
		return this.state;
	}

	returnTotalNum() {
		let cols = this.refs.hot.hotInstance.countCols();
		let rows = this.refs.hot.hotInstance.countRows();
		return Number(cols) * Number(rows);
	}

	afterColumnResize(col, size) {
		let colWidths = this.props.settings.colWidths;
		colWidths.splice(col, 1, size);
		this.setState({
			colWidths
		});
	}

	afterRowResize(row, size) {
		let rowHeights = this.props.settings.rowHeights;
		rowHeights.splice(row, 1, size);
		this.setState({
			rowHeights
		});
	}
	beforeCopy = (data, coords) => {
		let copyData = '';
		data.forEach((array, index) => {
			array.forEach((item, idx) => {
				if (item && (item.title || item.title === null || item.key || item.style)) {
					let text = item.title ? item.title : ' ';
					copyData += text + '\t';
				} else {
					if (item !== null && item !== '' && item[0] !== null && item[0].length > 0 && item[0][0] !== '') {
						copyData += item[0][0] + '\t';
					} else {
						copyData += ' ' + '\t';
					}
				}
			});
			copyData += '\n';
		});
		let reportTextarea = document.getElementsByClassName('hidden-textarea');
		if (reportTextarea[0]) {
			document.body.removeChild(reportTextarea[0]);
		}
		let textarea = document.createElement('textarea', { id: 'aaa' });
		textarea.className = 'hidden-textarea';
		document.body.appendChild(textarea);
		textarea.value = copyData;
		textarea.select();
		document.execCommand('Copy');
		return false;
	};

	afterDocumentKeyDown = (e) => {
		let classes = e.target.parentElement.className;
		if (classes.includes('hanson-export')) e.stopImmediatePropagation();
	};

	shouldComponentUpdate(nextProps, nextState) {
		return this.props.settings.data !== nextProps.settings.data ||
			this.props.settings.mergeCells !== nextProps.settings.mergeCells || 
			this.props.settings.fixedRowsTop !== nextProps.settings.fixedRowsTop || 
			this.props.settings.fixedColumnsLeft !== nextProps.settings.fixedColumnsLeft
	}

	render() {
		return (
			<HotTable
				id="handsontable"
				ref="hot"
				root="hot"
				search="true"
				settings={this.props.settings}
				afterColumnResize={this.afterColumnResize.bind(this)}
				afterRowResize={this.afterRowResize.bind(this)}
				afterSelectionEnd={this.props.afterSelection.bind(this, this.refs.hot)}
				afterOnCellMouseDown={this.props.afterOnCellMouseDown}
				afterGetColHeader={this.props.afterGetColHeader}
				beforeCopy={this.beforeCopy}
				afterDocumentKeyDown={this.afterDocumentKeyDown}
			/>
		);
	}
}
