let searchId = 'light_report';
import { ajax } from 'nc-lightapp-front';
import Utils from '../../../../public/utils';
const { getGlobalStorage } = Utils;
export default function(props) {
	let _this = this;
	const { createUIDom } = props;
	const { appcode, pageCode } = this.commonParams;
	if (this.props.isSearchFromDesign) {
		ajax({
			url: '/nccloud/platform/templet/querypage.do',
			data: {
				templetid: this.props.isSearchFromDesign.templetid,
				appcode: this.props.isSearchFromDesign.appcode,
				pagecode: this.props.isSearchFromDesign.pagecode
			},
			success: (templedata) => {
				disposeSearch.call(this, templedata);
			}
		});
	} else {
		createUIDom(
			{
				appcode,
				pagecode: pageCode
			},
			(templedata) => {
				disposeSearch.call(_this, templedata);
			}
		);
	}
}

function modifierMeta(props, meta) {
	// 修改列渲染样式
	// meta[searchId].items.map((ele) => {
	// 	ele.visible = true
	// })
	return meta;
}

function disposeSearch(templedata) {
	let meta = templedata.template || templedata.data;
	if (meta) {
		meta = modifierMeta(this.props, meta);
		if (this.props.disposeSearch) {
			meta = this.props.disposeSearch(meta, this.props);
			this.props.meta.setMeta(meta, () => {
				if (this.props.setDefaultVal) {
					if (getGlobalStorage('localStorage', 'LinkReport') || getGlobalStorage('sessionStorage', 'LinkReport')) {
						//单据联查报表是否走自己的设默认值
						this.props.setDefaultVal(searchId, this.props, true, templedata);
					} else {
						this.props.setDefaultVal(searchId, this.props, false, templedata);
					}
				}
			});
		} else {
			this.props.meta.setMeta(meta, () => {
				if (this.props.setDefaultVal) {
					if (getGlobalStorage('localStorage', 'LinkReport') || getGlobalStorage('sessionStorage', 'LinkReport')) {
						this.props.setDefaultVal(searchId, this.props, true, templedata);
					} else {
						this.props.setDefaultVal(searchId, this.props, false, templedata);
					}
				}
			});
		}
		this.commonParams = {
			...this.commonParams,
			oid: meta[searchId].oid || '',
			queryAreaCode: meta[searchId].code || ''
		};
	}
	let sessionFlag = getGlobalStorage('localStorage', 'LinkReport') || getGlobalStorage('sessionStorage', 'LinkReport');
	if (sessionFlag) {
		this.getData();
	}
}
