import sendSearchFun from './sendSearch';
import Utils from '../../../../public/utils';
import { isSearchRender } from './isSearchRender';
const { disposeSearch } = Utils;
// 点击查询，获取查询区数据
/***
 * props: 原始props
 * expandProps: 拓展后的props
 * items: 查询条件
 */
export default function clickSearchBtn(expandProps, props, items, type, queryInfo) {
	if (items === false) {
		return;
	}
	let condition,
		custcondition = {};
	if (expandProps.expandSearchVal) {
		let result = disposeSearch(expandProps.expandSearchVal(items, props, type, queryInfo));
		if (result === false) {
			return;
		}
		if (result === true) return true;
		condition = result.condition;
	} else {
		let obj = disposeSearch(items);
		condition = obj.condition;
	}
	if (expandProps.custconditions) {
		custcondition = expandProps.custconditions(items, props, type, queryInfo);
		if (custcondition === false) return;
		if (custcondition === true) return true;
	}
	this.custcondition = custcondition;
	this.isClickShowAllCount = true;
	sendSearchFun(this, expandProps, props, condition, type, queryInfo, custcondition);
}
