import React, { Component } from 'react';
import { base, high } from 'nc-lightapp-front';
const { NCFormControl, NCRadio, NCCheckbox, NCIcon } = base;
const { Refer } = high;
const { ReferLoader } = Refer;
import UseridsRefer from '../../../public/uap/index.js';
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');
let now = new Date();
now.setTime(now.getTime());

const CheckboxGroup = NCCheckbox.CheckboxGroup;
export default class TaskForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    const MSGVARS = {
      0: `${langCheck('reportTimeMultiLang', '100303-000012')}${now.getFullYear() + "-" + (now.getMonth() + 1) + "-" + now.getDate()}`, //系统日期
      1: `${langCheck('reportTimeMultiLang', '100303-000016')}${now.getFullYear()}`, //年
      2: `${langCheck('reportTimeMultiLang', '100303-000015')}${now.getMonth() + 1}`, //月
      3: `${langCheck('reportTimeMultiLang', '100303-000014')}${now.getDate()}`, //日
      4: `${langCheck('reportTimeMultiLang', '100303-000017')}${now.getHours() + ":" + (now.getMinutes()) + ":" + now.getSeconds()}`, //时间
      5: `${langCheck('reportTimeMultiLang', '100303-000047')}`, //示例报表名称
      6: `${langCheck('reportTimeMultiLang', '100303-000048')}`, //示例菜单名称
      7: `${langCheck('reportTimeMultiLang', '100303-000049')}` //示例接收人
    };
    const { status, pageData } = this.props;
    const {
      taskName,
      taskNote,
      timeConfig,
      receivers,
      fileType,
      taskExecRoom,
      msgTitle,
      msgVars,
      msgText } = pageData;
    let receiversText = '';
    receivers && typeof (receivers) != 'string' && receivers.map(item => {
      if (item && item.refname) {
        receiversText += `${item.refname},`
      }
    })
    let varsText = '';
    msgVars && msgVars.map(item => {
      if (item) {
        varsText += `${MSGVARS[item]},`
      }
    })
    return (
      <div className="task-form">
        <div className="box large">
          <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000000')}:</span> {/**任务名称 */}
          {status == 'edit' ? <NCFormControl value={taskName} onChange={val => this.props.onChange('taskName', val)} /> : <span>{taskName}</span>}
        </div>
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000001')}:{/**说明 */}</span>
          {status == 'edit' ? <NCFormControl value={taskNote} onChange={val => this.props.onChange('taskNote', val)} /> : <span>{taskNote}</span>}
        </div>
        <h3 className="title">{langCheck('reportTimeMultiLang', '100303-000002')}{/*基础设置*/}</h3>
        <div className="box">
          <div className="info">
            <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000003')}:{/**定时设置 */}</span>
            {status == 'edit' ?
              <div className="task-time" onClick={this.props.showTimeModal}>
                {timeConfig.occurType == '3' ? `${langCheck('reportTimeMultiLang', '100303-000050')}` : timeConfig.occurType == '4' ? `${langCheck('reportTimeMultiLang', '100303-000035')}` : timeConfig.occurType == '5' ? `${langCheck('reportTimeMultiLang', '100303-000015')}` : ''}
                <i className="icon iconfont icon-canzhaozuixin"></i>
              </div>
              : <span>{timeConfig.occurType == '3' ? `${langCheck('reportTimeMultiLang', '100303-000050')}` : timeConfig.occurType == '4' ? `${langCheck('reportTimeMultiLang', '100303-000035')}` : timeConfig.occurType == '5' ? `${langCheck('reportTimeMultiLang', '100303-000015')}` : ''}</span>}
          </div>
          <div className="info">
            <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000004')}:</span> {/**接收人 */}
            {status == 'edit' ? <UseridsRefer
              queryCondition={{
                isAllUserVisible: true,
                showSysAdmin: 'true',
                showGroupAdmin: 'true'
              }}
              value={receivers}
              onChange={val => this.props.onChange('receivers', val)}
            /> : <span>{receiversText}</span>}</div>
        </div>
        <div className="box">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000005')}:</span>{/**附件类型 */}
          {status == 'edit' ? <NCRadio.NCRadioGroup name="fileType" selectedValue={fileType} onChange={val => this.props.onChange('fileType', val)}>
            <NCRadio value={'0'}>Excle</NCRadio>
            <NCRadio value={'1'}>PDF</NCRadio>
            <NCRadio value={'2'}>CSV</NCRadio>
          </NCRadio.NCRadioGroup> : <span>{fileType == 0 ? 'Excle' : fileType == 1 ? 'PDF' : fileType == 2 ? 'CSV' : ''}</span>}
        </div>
        <h3 className="title">{langCheck('reportTimeMultiLang', '100303-000006')}</h3>{/**高级设置 */}
        <div className="box">
          <div className="info">
            <span className="lable">{langCheck('reportTimeMultiLang', '100303-000007')}:</span>{/**时区 */}
            {status == 'edit' ? <ReferLoader refcode="uapbd/refer/pubinfo/TimezoneDefaultGridRef/index" value={taskExecRoom.exectimezone} onChange={val => this.props.onChange('exectimezone', val)} /> : <span>{taskExecRoom.exectimezone && taskExecRoom.exectimezone.refname}</span>}
          </div>
          <div className="info">
            <span className="lable">{langCheck('reportTimeMultiLang', '100303-000008')}:</span>{/**语种 */}
            {status == 'edit' ? <ReferLoader refcode="uap/refer/riart/multiLangDefaultTableRef/index" value={taskExecRoom.execlanguage} onChange={val => this.props.onChange('execlanguage', val)} /> : <span>{taskExecRoom.execlanguage && taskExecRoom.execlanguage.refname}</span>}
          </div>
        </div>
        <div className="box">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000009')}:</span>{/**用户 */}
          {status == 'edit' ? <NCRadio.NCRadioGroup name="isCurUser" selectedValue={taskExecRoom.isCurUser} onChange={val => this.props.onChange('isCurUser', val)}>
            <NCRadio value={'0'}>{langCheck('reportTimeMultiLang', '100303-000010')}</NCRadio>{/**当前用户 */}
            <NCRadio value={'1'}>{langCheck('reportTimeMultiLang', '100303-000004')}</NCRadio>{/**接收人 */}
          </NCRadio.NCRadioGroup> : <span>{taskExecRoom.isCurUser == '0' ? `${langCheck('reportTimeMultiLang', '100303-000010')}` : `${langCheck('reportTimeMultiLang', '100303-000004')}`}</span>}
        </div>
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000011')}:</span>{/**消息标题 */}
          {status == 'edit' ? <NCFormControl value={msgTitle} onChange={val => this.props.onChange('msgTitle', val)} /> : <span>{msgTitle}</span>}
        </div>

        {status == 'edit' && <div className="box checkbox">
          <CheckboxGroup value={msgVars} onChange={val => this.props.onChange('msgVars', val)}>
            <NCCheckbox colors="primary" value="7">{langCheck('reportTimeMultiLang', '100303-000004')}</NCCheckbox>{/**接收人 */}
            <NCCheckbox colors="primary" value="0">{langCheck('reportTimeMultiLang', '100303-000012')}</NCCheckbox>{/**系统日期 */}
            <NCCheckbox colors="primary" value="6">{langCheck('reportTimeMultiLang', '100303-000013')}</NCCheckbox>{/**菜单名称 */}
            <NCCheckbox colors="primary" value="3">{langCheck('reportTimeMultiLang', '100303-000014')}</NCCheckbox>{/**日 */}
            <NCCheckbox colors="primary" value="2">{langCheck('reportTimeMultiLang', '100303-000015')}</NCCheckbox>{/**月 */}
            <NCCheckbox colors="primary" value="1">{langCheck('reportTimeMultiLang', '100303-000016')}</NCCheckbox>{/**年 */}
            <NCCheckbox colors="primary" value="4">{langCheck('reportTimeMultiLang', '100303-000017')}</NCCheckbox>{/**时间 */}
            <NCCheckbox colors="primary" value="5">{langCheck('reportTimeMultiLang', '100303-000018')}</NCCheckbox>{/**报表名称 */}
          </CheckboxGroup>
        </div>}
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000019')}:</span>{/**示例 */}
          <span>{varsText}</span>
        </div>

        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000020')}:</span>{/**消息正文 */}
          {status == 'edit' ? <NCFormControl value={msgText} onChange={val => this.props.onChange('msgText', val)} /> : <span>{msgText}</span>}
        </div>
      </div>
    );
  }
}
