import { ajax, toast } from 'nc-lightapp-front';
import { tableRender } from './methods';
import Utils from '../../../../public/utils';
const { addClassName, deWeight, langCheck } = Utils;

/**
	 * 筛选
	 * @param {*} data 选中单元格的坐标内容
	 */
export const getFilterInfo = function(data) {
	//后台根据行列坐标查询是数值型还是字符型，字符型会返回下拉数据
	ajax({
		url: '/nccloud/report/widget/lightreport_filterInfo.do',
		data,
		success: (res) => {
			if (res.success) {
				let data = res.data;
				if (data.isStatistic) {
					toast({
						content: langCheck('reportMultiLang', '100301-000015'),
						color: 'warning'
					}); /* 国际化处理： 您选择的是统计字段，暂不支持筛选。*/
					return;
				}
				let selectData = [];
				if (data.filterValues) {
					selectData = data.filterValues;
				}
				//筛选modal选择
				this.refs.filterModal.setState({
					showModal: true,
					selectData,
					filterType: data.fieldType,
					fieldName: data.fieldName || ''
				});
			}
		}
	});
};

// filterModal提交
export const getFilterData = function() {
	const { pager, operationQueue } = this.state;
	const { condition } = this.pageParams;
	let coords = this.coords;
	let parameter = {
		row: coords ? coords.row.toString() : '',
		column: coords ? coords.col.toString() : '',
		operationQueue: operationQueue,
		pageInfo: {
			pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
			pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
		},
		querycondition: condition
	};
	// 提交筛选器
	let { operValues, filterValues, filterType } = this.refs.filterModal.state;
	if (filterType === 'NUMERIC') {
		//数值
		let data = Object.assign({}, parameter, this.commonParams, {
			operValues: operValues
		});
		if (
			(operValues.firstValue && operValues.firstRelationOper) ||
			(operValues.secondValue && operValues.secondRelationOper)
		) {
			setFilterFun.call(this, data);
		}
	} else {
		// 字符
		let data = Object.assign({}, parameter, this.commonParams, {
			filterValues: filterValues
		}); // 字符串型
		setFilterFun.call(this, data);
	}
};

function setFilterFun(data) {
	// 发送请求处理
	let { filterColIndexArray } = this.filterStyle;
	let coords = this.coords;
	ajax({
		url: '/nccloud/report/widget/lightreport_filter.do',
		data,
		success: (res) => {
			if (res.success) {
				filterColIndexArray.push(coords.col);
				this.filterStyle = {
					filterColIndexArray: deWeight(filterColIndexArray),
					cancelFilterIndex: null
				};
				tableRender(this, res.data);
				closeFilterModal.call(this);
				this.initParams.dataRowNum = res.data.dataRowNum;
				addClassName(document.getElementsByClassName('fitletDisabled')[0]);
				addClassName(document.getElementsByClassName('sortDisabled')[0]);
				let sortFilterBtns = document.getElementsByClassName('sortAndFilterArea');
				for (let i = 0; i < sortFilterBtns.length; i++) {
					addClassName(sortFilterBtns[i]);
				}
			}
		}
	});
}

/**
	 * 取消筛选 
	 * @param {*} 
	 */
export const cancelFilter = function(data) {
	const { reportType } = this.pageParams;
	let { filterColIndexArray } = this.filterStyle;
	if (filterColIndexArray.indexOf(Number(data.column)) < 0 && reportType != 1) {
		toast({
			content: `${langCheck('reportMultiLang', '100301-000041')}。`,
			color: 'warning'
		}); /* 国际化处理： 您选择的此列未进行过筛选*/
		return;
	}
	ajax({
		url: '/nccloud/report/widget/lightreport_filter.do',
		data,
		success: (res) => {
			if (res.success) {
				let index = filterColIndexArray.indexOf(Number(data.column));
				filterColIndexArray.splice(index, 1);
				this.filterStyle = {
					filterColIndexArray,
					cancelFilterIndex: data.column
				};
				this.isClickShowAllCount = false;
				this.initParams.dataRowNum = res.data.dataRowNum;
				tableRender(this, res.data);
				addClassName(document.getElementsByClassName('fitletDisabled')[0]);
			}
		}
	});
};

//关闭模态框
export const closeFilterModal = function() {
	let { area } = this.state;
	let coords = this.coords;
	if (coords.row <= area[2] && coords.col <= area[3]) {
		this.isRange = false;
	} else {
		this.isRange = true;
	}

	this.refs.filterModal.setState({
		operValues: {
			logicOper: '0',
			firstRelationOper: '',
			secondRelationOper: '',
			firstValue: '',
			secondValue: ''
		},
		filterValues: [],
		showModal: false,
		filterType: null
	});
};
