/**
 * 初始化时候：
 *  1.参数传appcode则使用appcode，传pk_storyboard则使用pk_storyboard，都传则使用pk_storyboard
 *  2.参数pk_params有则传，没有则不传
 *  3.isInitData是true则请求渲染数据，false或者不传则请求默认展示的数据（接口不同）
 *
 */

import React, { Component } from 'react';
import { ajax, pageTo } from 'nc-lightapp-front'

const previewUrlPrefix = 'http://172.20.56.13:8800/analysis/';

class GraphicReport extends Component {
    constructor(props) {
        super(props);
        this.graphicRef = null;
        this.time_offset = (-8 - new Date().getTimezoneOffset() / 60).toString();
        let appcode = (window.location.search && window.location.search.split('=')[1]) || ''
        this.appCode = appcode || pageTo.getUrlParam('appcode') || pageTo.getSearchParam('c') || '';
        this.state = {
            iframeUrl: '',
            pk_storyboard: ''
        };
        this.timer = null;
    }

    componentDidMount() {
        if (this.props.params && this.props.params.pk_storyboard) {
            this.getParams(this.props.params.pk_storyboard, this.appCode, this.props.params);
        }
    }

    componentWillReceiveProps(nextProps) {
        this.getParams(nextProps.params.pk_storyboard, this.appCode, nextProps.params);
    }

    getParams = (pk_storyboard, appCode, params) => {
        let self = this;
        let isWidget = params.appType === 'widget';
        let analysis_params = params && params.analysis_params || {}; //分析型参数
        let search_params = params && params.search_params || {}; //查询区参数
        let reportRowData = params && params.reportRowData || '';
        let hideTopBarFromBusi = params && params.hideTopBar;
        let { iframeUrl } = this.state;
        ajax({
            url: '/nccloud/report/graphic/storyboardpreviewaction.do',
            data: {
                pk_storyboard: pk_storyboard || '',
                appCode: appCode || '',
                time_offset: this.time_offset,
                analysis_params,
                reportRowData,
                ...search_params
            },
            success: res => {
                const data = res.data;
                let { urlPrefix, dataId, nccToken, V_Token, sysTenantId, analysis_params, usercode, dsname, hideTopBar, webmodel_url, language } = data;
                let hdtopbar = hideTopBarFromBusi == true || hideTopBarFromBusi == 'true' ? 'hdtopbar&' : hideTopBar == true ? 'hdtopbar&' : '';
                let webmodel = '';
                if (webmodel_url) {
                    webmodel = `&webmodel_url=${webmodel_url}`;
                }
                let language_type = '';
                if (language) {
                    language_type = `&language=${language}`;
                }
                this.urlPrefix = urlPrefix;
                if (!iframeUrl) {
                    this.setState({
                        iframeUrl:
                            `${this.urlPrefix}/analysis/${dataId}?${hdtopbar}hb=share,edit,case,casebtn,close,a_edit&nccToken=${nccToken}&dataId=${dataId}&usercode=${usercode}&dsname=${dsname}&V-Token=${V_Token}&sysTenantId=${sysTenantId}&${analysis_params}${webmodel}${language_type}`
                    });
                } else {
                    this.graphicRef && this.graphicRef.contentWindow && this.graphicRef.contentWindow.postMessage({
                        type: 'refresh',
                        nccToken,
                        'V-Token': V_Token,
                        sysTenantId,
                        search_params: JSON.parse(JSON.stringify(search_params)),
                        analysis_params,
                        language,
                        webmodel_url,
                        hb: 'share,edit,close,a_edit,case,casebtn',
                        hdtopbar: hdtopbar == '' ? 'false' : 'true'
                    }, this.urlPrefix);
                }

                this.setState({
                    pk_storyboard: pk_storyboard || '',
                });

                if (isWidget) {
                    let TimingTask = data.TimingTask;
                    console.log('定时刷新秒数:', TimingTask);
                    self.timer && clearInterval(self.timer);
                    self.timer = setInterval(() => {
                        try {
                            this.graphicRef && this.graphicRef.contentWindow && this.graphicRef.contentWindow.postMessage({
                                type: 'refresh',
                                nccToken,
                                'V-Token': V_Token,
                                sysTenantId,
                                search_params: JSON.parse(JSON.stringify(search_params)),
                                language,
                                webmodel_url,
                                analysis_params,
                                hb: 'share,edit,close,a_edit,case,casebtn',
                                hdtopbar: hdtopbar == '' ? 'false' : 'true'
                            }, this.urlPrefix);
                        } catch (e) {
                            throw new Error(e);
                        }
                    }, TimingTask * 1000);
                }
                this.graphicRef = null;
            }
        })
    };

    render() {
        const { iframeUrl } = this.state;
        return (
            <iframe ref={(r) => (this.graphicRef = r)} src={iframeUrl} style={{ width: '100%', height: '100%' }}></iframe>
        )
    }
}


export default GraphicReport;
