import { tableRender } from './methods';
import { toast } from 'nc-lightapp-front';
import { initRollingFilterData, getRollingData } from './rollingFun';
import Utils from '../../../../public/utils';
const { showSearchToast, langCheck, setGlobalStorage } = Utils;

export const isSearchRender = function (res, condition, queryInfo) {
	let data = res.data;
	if (data.shouldHintForSmartMaxRow) {
		toast({ content: langCheck('reportMultiLang', '100301-000104'), color: 'warning' });
	} else {
		showSearchToast(data, this);
	}

	this.preloadMaxRow = data.preloadMaxRow;
	this.sortStyle.headerSwitch = null;
	this.isClickSearchBtn = true;
	this.sortStyle.headStyle = '';
	this.filterStyle.filterColIndexArray = [];
	this.sortStyle.sortStyles = [];
	this.isConnection = false; // 重置状态
	setGlobalStorage('sessionStorage', `trans${this.commonParams.opents}${this.commonParams.pk_report}`, data.transSaveObject || '')
	this.initParams = {
		...this.initParams,
		dataRowNum: data.dataRowNum,
		noDropdownPrint: false,
		excelPrintMaxRow: data.excelPrintMaxRow,
		pdfPrintMaxRow: data.pdfPrintMaxRow,
		key_CUR_USER_ID: data.key_CUR_USER_ID,
		isShowZero: data.isShowZero //当返回为false时，表格区字段是0不显示
	};
	this.pageParams = {
		...this.pageParams,
		FreeReport_QueryTreeVo: data.FreeReport_QueryTreeVo || '',
		reportType: data.reportType,
		condition
	};
	if (data.repCondition && data.repCondition.pageInfo.length > 0) {
		this.initParams = {
			...this.initParams,
			repCondition: data.repCondition,
			pagerData: {
				...this.initParams.pagerData,
				type: data.repCondition.pageType,
				pageInfo: data.repCondition.pageInfo,
				modalRollingArray: initRollingFilterData.call(
					this,
					data.repCondition.dataArr,
					data.repCondition,
					'search'
				),
				count: 0
			}
		};
		this.state.rollingPagerData = [];
		getRollingData.call(this, 'search');
	} else {
		this.initParams = {
			...this.initParams,
			pagerData: {
				...this.initParams.pagerData,
				type: -1
			}
		};
	}
	const { getUrlParam, setUrlParam } = this.props;
	const row = getUrlParam('row'); //动态url传参
	if (row) setUrlParam({ row: '' });

	// if (this.state.graphicParams.needLoadWhenQuery) {
	this.setState({
		graphicParams: {
			...this.state.graphicParams,
			search_params: queryInfo
		}
	})
	//}
	tableRender(this, res.data);
};
