import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCSelect } = base;
const NCOption = NCSelect.NCOption;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class Rolling extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	onSelectChange = (index, val) => {
		let valArray = val.split('$$$$');
		let obj = { id: index + 1, relValue: valArray[0], disValue: valArray[1] };
		this.props.checkPaper(obj, index, 'select');
	};

	onUpDownClick = (pagerData) => {
		const { modalRollingArray, pageInfo } = pagerData;
		if (modalRollingArray == null || modalRollingArray[0].perch == 0 || pageInfo.length > 1) {
			return;
		}
		modalRollingArray[0].forEach((item, index) => {
			item.orderIndex = index;
		});
		this.props.showPagerClick(0, modalRollingArray, pageInfo);
	};

	render() {
		const { rollingPagerData, pagerData, allData } = this.props;
		let disabled;
		if (allData && allData.length == 1) {
			disabled = 'disabled';
		}
		if (!pagerData) return null;

		return (
			<div style={{ display: 'flex', alignItems: 'center', marginLeft: '10px' }}>
				{(pagerData.type == 1 || pagerData.type == 2) && (
					<div className="rolling">
						{pagerData &&
							pagerData.upDownRollingArray &&
							pagerData.upDownRollingArray.map((item, index) => {
								return (
									<div className="pager">
										<h2 className="pager-title" title={pagerData.pageInfo[index].name}>
											{pagerData.pageInfo[index].name}
										</h2>
										<span
											title={item.disValue || ''}
											className="up-down next"
											onClick={this.onUpDownClick.bind(this, pagerData)}
										>
											{item.disValue || ' '}
										</span>
									</div>
								);
							})}
						{pagerData.flag && (
							<span>
								{pagerData.type == 2 && (
									<i
										onClick={this.props.rollingUpDown.bind(this, 'first')}
										className={'icon iconfont icon-shangyiye border-left-none ' + disabled}
									/>
								)}
								<i
									onClick={this.props.rollingUpDown.bind(this, 'prev')}
									className={'icon iconfont icon-jiantouzuo border-left-none ' + disabled}
								/>
								<i
									onClick={this.props.rollingUpDown.bind(this, 'next')}
									className={'icon iconfont icon-jiantouyou border-left-none ' + disabled}
								/>
								{pagerData.type == 2 && (
									<i
										onClick={this.props.rollingUpDown.bind(this, 'last')}
										className={'icon iconfont icon-xiayiye border-left-none ' + disabled}
									/>
								)}
							</span>
						)}
					</div>
				)}
				{pagerData.type == 0 && (
					<div className="rolling">
						{pagerData.modalRollingArray.map((item, index) => {
							if (
								pagerData.pageInfo[index].choiceStyle == 2 ||
								pagerData.pageInfo[index].choiceStyle == 3
							) {
								return (
									<div className="pager">
										<h2 className="pager-title" title={pagerData.pageInfo[index].name}>
											{pagerData.pageInfo[index].name}
										</h2>
										<NCSelect
											style={{ width: 240 }}
											placeholder={langCheck('reportMultiLang', '100301-000069')} /* 国际化处理： 请选择*/
											value={
												rollingPagerData[index] ? (
													rollingPagerData[index].disValue
												) : (
													pagerData.pageInfo != null &&
													pagerData.pageInfo[index].selectedDisValue
												)
											}
											//placeholder={(pagerData.modalRollingArray != null && pagerData.modalRollingArray[0].perch == undefined) ? pagerData.modalRollingArray[0][0].disValue : ''}
											onChange={this.onSelectChange.bind(this, index)}
										>
											{pagerData.modalRollingArray !== null &&
											pagerData.modalRollingArray[0].perch == undefined ? (
												pagerData.modalRollingArray[index].map((ele) => {
													return (
														<NCOption value={ele.relValue + '$$$$' + ele.disValue}>
															{ele.disValue}
														</NCOption>
													);
												})
											) : null}
										</NCSelect>
									</div>
								);
							} else if (
								pagerData.pageInfo[index].choiceStyle == 0 ||
								pagerData.pageInfo[index].choiceStyle == 1 ||
								pagerData.pageInfo[index].choiceStyle == 4 ||
								pagerData.pageInfo[index].choiceStyle == undefined
							) {
								return (
									<div className="pager">
										<h2 className="pager-title" title={pagerData.pageInfo[index].name}>
											{pagerData.pageInfo[index].name}
										</h2>
										<span
											className="up-down"
											onClick={this.props.showPagerClick.bind(
												this,
												index,
												pagerData.modalRollingArray,
												pagerData.pageInfo
											)}
										>
											{rollingPagerData[index] ? (
												rollingPagerData[index].disValue
											) : (
												pagerData.pageInfo != null && pagerData.pageInfo[index].selectedDisValue
											)}
											<b className={'icon iconfont icon-canzhao'} />
										</span>
									</div>
								);
							}
						})}
						<span className="filter" onClick={this.props.rollingFilterClick}>
							{langCheck('reportMultiLang', '100301-000033')}
							{/* 国际化处理： 筛选*/}
						</span>
					</div>
				)}
			</div>
		);
	}
}
