export const tableRender = function(that, data) {
	// ----------渲染表格。处理数据
	let result = data;
	let DATAARR = []; //表格数据
	const DEFAULTCOLLEN = 25; //默认最少行数
	const DEFAULTROWLEN = 10; //默认最少列数
	let cellWidth = []; //存放单元格宽度数组
	let fldAreaArray = []; //存放每列有效区域
	let seperateCol = 0; //默认冻结区域
	let seperateRow = 0; //默认冻结区域
	let pager = result.pager || {
		allRowCount: result.reportType == '2' ? null : '0',
		pageCount: '1',
		pageIndex: '1',
		pageRowCount: '0',
		pageSize: '200'
	};

	let area = [], fldAreas = [], calcFields = [];
	if ((result &&
		result.CellsModel &&
		result.CellsModel.AreaDatas &&
		result.CellsModel.AreaDatas.length == 1)) {
			area = result.CellsModel.AreaDatas[0].area;
			fldAreas = result.CellsModel.AreaDatas[0].fldAreas;
			calcFields = result.CellsModel.AreaDatas[0].calcFields
	} else if ((result &&
		result.CellsModel &&
		result.CellsModel.AreaDatas &&
		result.CellsModel.AreaDatas.length > 1)) {
			for (let i = 0; i < result.CellsModel.AreaDatas.length; i++) {
				area = area.concat(result.CellsModel.AreaDatas[i].area);
				fldAreas = fldAreas.concat(result.CellsModel.AreaDatas[i].fldAreas);
				calcFields = calcFields.concat(result.CellsModel.AreaDatas[i].calcFields)
			}
	}

	// let area =
	// 	(result &&
	// 		result.CellsModel &&
	// 		result.CellsModel.AreaDatas &&
	// 		result.CellsModel.AreaDatas[0] &&
	// 		result.CellsModel.AreaDatas[0].area) ||
	// 	[];
	// let fldAreas =
	// 	(result &&
	// 		result.CellsModel &&
	// 		result.CellsModel.AreaDatas &&
	// 		result.CellsModel.AreaDatas[0] &&
	// 		result.CellsModel.AreaDatas[0].fldAreas) ||
	// 	[];
	// let calcFields =
	// 	(result &&
	// 		result.CellsModel &&
	// 		result.CellsModel.AreaDatas &&
	// 		result.CellsModel.AreaDatas[0] &&
	// 		result.CellsModel.AreaDatas[0].calcFields) ||
	// 	[];
	let newAreas = [];
	for (let i = 0; i < fldAreas.length; i++) {
		let isExists = false;
		for (let j = 0; j < calcFields.length; j++) {
			if (calcFields[j] == fldAreas[i].fldName) {
				isExists = true;
				break;
			}
		}
		if (isExists) {
			continue;
		}
		fldAreas[i].fldArea.forEach((item, index) => {
			fldAreaArray[item[1]] = [];
		});
		newAreas.push(fldAreas[i]);
	}
	newAreas.forEach((areas, index) => {
		areas.fldArea.forEach((item, index) => {
			fldAreaArray[item[1]].push({ startRow: item[0], endRow: item[2], id: areas.fldName });
		});
	});

	if (
		//处理表格数据
		result.CellsModel &&
		result.CellsModel.DynamicModel &&
		result.CellsModel.DynamicModel[0] &&
		result.CellsModel.DynamicModel[0].cellsArray
	) {
		var tdStyle = checkClass(result.CellsModel.DynamicModel[0]);
		cellWidth = result.CellsModel.columnHeader.headerArray.concat();
		result.CellsModel.DynamicModel[0].cellsArray.forEach((items, row) => {
			let itemsArr = [];
			items.forEach((item, col) => {
				if (result.CellsModel.columnHeader.headerArray[col] == 0) {
					cellWidth[col] = 0.01;
					item = null;
				}
				itemsArr.push([ item || null ]);
			});
			DATAARR.push(itemsArr);
		});

		if (DATAARR.length < DEFAULTCOLLEN) {
			let initArray = new Array(DEFAULTCOLLEN - DATAARR.length);
			for (let i = 0; i < initArray.length; i++) {
				initArray[i] = [ null ];
			}
			DATAARR = DATAARR.concat(initArray);
		}
		if (DATAARR[0].length < DEFAULTROWLEN) {
			let initRowArray = new Array(DEFAULTROWLEN - DATAARR[0].length);
			for (let i = 0; i < initRowArray.length; i++) {
				initRowArray[i] = [ null ];
			}
			DATAARR[0] = DATAARR[0].concat(initRowArray);
		}
		let rowHeights = result.CellsModel.rowHeader.headerArray;
		for (let i = 0; i < rowHeights.length; i++) {
			if (rowHeights[i] < 23) {
				rowHeights[i] = 23;
			}
		}

		let lockSet = result.CellsModel.tableSetting.lockSet;
		seperateCol =
			that.state.settings.fixedColumnsLeft != 0
				? that.state.settings.fixedColumnsLeft
				: lockSet.isFreezing && that.isBlock !== false ? lockSet.seperateCol : 0;
		seperateRow =
			that.state.settings.fixedRowsTop != 0
				? that.state.settings.fixedRowsTop
				: lockSet.isFreezing && that.isBlock !== false ? lockSet.seperateRow : 0;
		if (
			that.isBlock !== false &&
			(that.state.settings.fixedColumnsLeft != 0 ||
				that.state.settings.fixedRowsTop != 0 ||
				(lockSet.isFreezing && lockSet.seperateCol != 0) ||
				(lockSet.isFreezing && lockSet.seperateRow != 0))
		)
			that.isBlock = true;
		that.setState({
			tdStyle,
			result,
			area,
			pager,
			fldAreaArray,
			totalObj: {
				...that.state.totalObj,
				sumNum: '',
				count: ''
			},
			operationQueue: result.operationQueue || '',
			settings: {
				...that.state.settings,
				data: DATAARR,
				fixedRowsTop: seperateRow,
				fixedColumnsLeft: seperateCol,
				colWidths: cellWidth,
				rowHeights,
				rowHeaders: result.CellsModel.tableSetting.styleSet.showRowHeader,
				colHeaders: result.CellsModel.tableSetting.styleSet.showColHeader,
				mergeCells: areaCellsFun(result)
			}
		});
		that.commonParams.transSaveObject = result.transSaveObject || that.commonParams.transSaveObject;
	}
};

function checkClass(data) {
	//获取样式数据
	let aligns = data.aligns;
	let fonts = data.fonts;
	let lines = data.lines;
	let formats = data.formats;
	if (aligns) {
		var newAligns = {};
		aligns.forEach((item) => {
			newAligns[item.id] = item.detail;
		});
	}
	if (fonts) {
		var newFonts = {};
		fonts.forEach((item) => {
			newFonts[item.id] = item.detail;
		});
	}
	if (lines) {
		var newLines = {};
		lines.forEach((item) => {
			newLines[item.id] = item.detail;
		});
	}
	if (formats) {
		var newFormats = {};
		formats.forEach((format) => {
			var oneFormat = {
				font: newFonts[format.detail.fontId],
				align: newAligns[format.detail.alignId],
				line: newLines[format.detail.lineId],
				isData: format.isData
			};
			newFormats[format.id] = oneFormat;
		});
	}
	return newFormats;
}

export const areaCellsFun = function(result) {
	//和并列数据提取
	let AREAARR = [];
	if (
		//表格合并列行
		result.CellsModel &&
		result.CellsModel.DynamicModel &&
		result.CellsModel.DynamicModel[0] &&
		result.CellsModel.DynamicModel[0].areaCells &&
		result.CellsModel.DynamicModel[0].areaCells.length > 0
	) {
		result.CellsModel.DynamicModel[0].areaCells.forEach((items) => {
			let areaObj = {
				row: Number(items[0]),
				col: Number(items[1]),
				rowspan: items[2] - items[0] + 1,
				colspan: items[3] - items[1] + 1
			};
			AREAARR.push(areaObj);
		});
	} else {
		AREAARR = [];
	}
	return AREAARR;
};

export const turnUpIcon = function(iconDOM) {
	iconDOM.classList.remove('icon-hangcaozuoxiala1');
	iconDOM.classList.remove('hangcaozuoxiala1');
	iconDOM.classList.add('icon-hangcaozuoxiangshang1');
	iconDOM.classList.add('hangcaozuoxiangshang1');
};

export const turnDownIcon = function(iconDOM) {
	iconDOM.classList.remove('icon-hangcaozuoxiangshang1');
	iconDOM.classList.remove('hangcaozuoxiangshang1');
	iconDOM.classList.add('icon-hangcaozuoxiala1');
	iconDOM.classList.add('hangcaozuoxiala1');
};
