import { toast } from 'nc-lightapp-front';
import Utils from '../../../public/utils';
const { langCheck } = Utils;
export const blockFun = function(that, type) {
	let coords = that.coords;
	if (coords.row == null || coords.row == undefined || coords.row == '') {
		toast({ color: 'warning', content: langCheck('tableMultiLang', '100302-000000') });/* 国际化处理： 请选择单元格进行冻结*/
		return;
	}
	let hansonWidth = document.getElementsByClassName('htCore')[0].scrollWidth;
	let tableWidth = document.getElementsByClassName('hot-table')[0].offsetWidth;
	if (hansonWidth <= tableWidth && (type == 'lockColumn' || type == 'lockRowColumn')) {
		toast({ content: langCheck('tableMultiLang', '100302-000001'), color: 'warning' });/* 国际化处理： 表格宽度小于屏幕宽度，暂不支持冻结列*/
		return;
	}
	let selectedInfo = that.tdSelected;
	if (!selectedInfo || selectedInfo.length < 1) {
		return;
	}
	let startRow = selectedInfo[0];
	let startCol = selectedInfo[1];
	let endRow = selectedInfo[2];
	let endCol = selectedInfo[3];
	let freezingNum = getRowOffset(that, startRow, startCol);

	let lockObj = {};
	lockObj.name = type;
	lockObj.parameter = {};
	if (type == 'lockRow') {
		setColumnOfLockParameter(lockObj.parameter, 'row', startRow, endRow);
		//更新锁定行列值
		updateFixedSetting(that, type, lockObj.parameter.row, '', selectedInfo, freezingNum);
		document.querySelector('.ht_master.handsontable div.wtHolder').scrollTop = 0;
	} else if (type == 'lockColumn') {
		setColumnOfLockParameter(lockObj.parameter, 'column', startCol, endCol);
		updateFixedSetting(that, type, '', lockObj.parameter.column, selectedInfo, freezingNum);
		document.querySelector('.ht_master.handsontable div.wtHolder').scrollLeft = 0;
	} else if (type == 'lockRowColumn') {
		setColumnOfLockParameter(lockObj.parameter, 'row', startRow, endRow);
		setColumnOfLockParameter(lockObj.parameter, 'column', startCol, endCol);
		updateFixedSetting(that, type, lockObj.parameter.row, lockObj.parameter.column, selectedInfo, freezingNum);
		document.querySelector('.ht_master.handsontable div.wtHolder').scrollTop = 0;
		document.querySelector('.ht_master.handsontable div.wtHolder').scrollLeft = 0;
	}
};

function getRowOffset(that, rowNum, colNum) {
	//获取冻结行列数
	var currentTd = that.refs.hansontable.refs.hot.hotInstance.getCell(rowNum, colNum, false);
	var tdOffset = currentTd.getBoundingClientRect();
	var rowOffset = 0;
	var colOffset = 0;
	var topReferOffset = 0;
	var topRefer = document.querySelector('.ht_clone_top_left_corner.handsontable .htCore thead');
	if (!topRefer) {
		topRefer = document.querySelector('header >div:nth-child(3)');
		topReferOffset = topRefer.getBoundingClientRect();
		var rowOffset = tdOffset.bottom - topReferOffset.bottom;
		var colOffset = tdOffset.right - topReferOffset.left;
	} else {
		topReferOffset = topRefer.getBoundingClientRect();
		var rowOffset = tdOffset.bottom - topReferOffset.bottom;
		var colOffset = tdOffset.right - topReferOffset.right;
	}

	var freezingNum = [];
	var heightSum = 0;
	if (currentTd.rowSpan > 1) {
		for (var j = 1; j < currentTd.rowSpan; j++) {
			heightSum += that.refs.hansontable.refs.hot.hotInstance.getRowHeight(rowNum + j);
		}
	}
	for (var i = rowNum; i >= 0; i--) {
		heightSum += that.refs.hansontable.refs.hot.hotInstance.getRowHeight(i);
		if (heightSum >= rowOffset - 22) {
			freezingNum[0] = rowNum - i;
			break;
		}
	}

	var widthSum = 0;
	if (currentTd.colSpan > 1) {
		for (var j = 1; j < currentTd.colSpan; j++) {
			widthSum += that.refs.hansontable.refs.hot.hotInstance.getColWidth(colNum + j);
		}
	}
	for (var i = colNum; i >= 0; i--) {
		widthSum += that.refs.hansontable.refs.hot.hotInstance.getColWidth(i);
		if (widthSum >= colOffset) {
			freezingNum[1] = colNum - i;
			break;
		}
	}

	return freezingNum;
}

function updateFixedSetting(that, type, rowNum, colNum, selectedInfo, freezingNum) {
	//表格总行数 总列数
	var countRows = that.refs.hansontable.refs.hot.hotInstance.countRows();
	var countCols = that.refs.hansontable.refs.hot.hotInstance.countCols();
	var areaCells = that.state.settings.mergeCells || [];
	var freezingAreaCells = [];
	if (type == 'lockRow') {
		var changeRowNum = rowNum - freezingNum[0]; //冻结时被舍去的行数
		//获取冻结所需的areaCells数据
		//0: row  1: col  rowspan: 2  colspan: 3
		areaCells.forEach((item) => {
			if (item[3] >= changeRowNum) {
				if (item[1] <= changeRowNum) {
					item[1] = changeRowNum;
				}
				freezingAreaCells.push(item);
			}
		});
		//对冻结所需areaCells数据,进行冻结状态合并单元格的处理,主要解决冻结分割处文字遮挡问题
		var mergeCells = freezingMergeCells(type, selectedInfo, freezingNum, freezingAreaCells);
		that.setState({
			settings: {
				...that.state.settings,
				fixedRowsTop: freezingNum[0],
				fixedColumnsLeft: 0,
				mergeCells: mergeCells
			}
		});
	} else if (type == 'lockColumn') {
		var changeColNum = colNum - freezingNum[1];

		areaCells.forEach((item) => {
			if (item[3] >= changeColNum) {
				if (item[1] <= changeColNum) {
					item[1] = changeColNum;
				}
				freezingAreaCells.push(item);
			}
		});
		var mergeCells = freezingMergeCells(type, selectedInfo, freezingNum, freezingAreaCells);
		that.setState({
			settings: {
				...that.state.settings,
				fixedRowsTop: 0,
				fixedColumnsLeft: freezingNum[1],
				mergeCells: mergeCells
			}
		});
	} else if (type == 'lockRowColumn') {
		var changeRowNum = rowNum - freezingNum[0];
		var changeColNum = colNum - freezingNum[1];

		areaCells.forEach((item) => {
			if (item[2] >= changeRowNum) {
				if (item[0] <= changeRowNum) {
					item[0] = changeRowNum;
				}
				freezingAreaCells.push(item);
			}
		});

		var freezingAreaCells2 = [];
		freezingAreaCells.forEach((item) => {
			if (item[3] >= changeColNum) {
				if (item[1] <= changeColNum) {
					item[1] = changeColNum;
				}
				freezingAreaCells2.push(item);
			}
		});
		var mergeCells = freezingMergeCells(type, selectedInfo, freezingNum, freezingAreaCells2);
		that.setState({
			settings: {
				...that.state.settings,
				fixedRowsTop: freezingNum[0],
				fixedColumnsLeft: freezingNum[1],
				mergeCells: mergeCells
			}
		});
	}
}

function freezingMergeCells(type, selectedInfo, freezingNum, areaCells) {
	var startRow = selectedInfo[0];
	var startCol = selectedInfo[1];

	var mergeCells = [];
	if (!areaCells) {
		return mergeCells;
	}

	if (type == 'lockRow') {
		//0: row  1: col  rowspan: 2  colspan: 3
		areaCells.forEach((areaCell) => {
			var viarable = startRow - freezingNum[0];
			if (areaCell[0] < startRow && areaCell[2] >= startRow) {
				mergeCells.push({
					row: areaCell[0] - viarable,
					col: areaCell[1],
					rowspan: startRow - areaCell[0],
					colspan: areaCell[3] - areaCell[1] + 1
				});
				mergeCells.push({
					row: startRow - viarable,
					col: areaCell[1],
					rowspan: areaCell[2] - startRow + 1,
					colspan: areaCell[3] - areaCell[1] + 1
				});
			} else {
				mergeCells.push({
					row: areaCell[0] - viarable,
					col: areaCell[1],
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: areaCell[3] - areaCell[1] + 1
				});
			}
		});
	} else if (type == 'lockColumn') {
		areaCells.forEach((areaCell) => {
			var viarable = startCol - freezingNum[1];
			if (areaCell[1] < startCol && areaCell[3] >= startCol) {
				mergeCells.push({
					row: areaCell[0],
					col: areaCell[1] - viarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: startCol - areaCell[1]
				});
				mergeCells.push({
					row: areaCell[0],
					col: startCol - viarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: areaCell[3] - startCol + 1
				});
			} else {
				mergeCells.push({
					row: areaCell[0],
					col: areaCell[1] - viarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: areaCell[3] - areaCell[1] + 1
				});
			}
		});
	} else if (type == 'lockRowColumn') {
		areaCells.forEach((areaCell) => {
			var rowViarable = startRow - freezingNum[0];
			var colViarable = startCol - freezingNum[1];
			if (areaCell[0] < startRow && areaCell[2] >= startRow) {
				mergeCells.push({
					row: areaCell[0] - rowViarable,
					col: areaCell[1] - colViarable,
					rowspan: startRow - areaCell[0],
					colspan: areaCell[3] - areaCell[1] + 1
				});
				mergeCells.push({
					row: startRow - rowViarable,
					col: areaCell[1] - colViarable,
					rowspan: areaCell[2] - startRow + 1,
					colspan: areaCell[3] - areaCell[1] + 1
				});
			} else if (areaCell[1] < startCol && areaCell[3] >= startCol) {
				mergeCells.push({
					row: areaCell[0] - rowViarable,
					col: areaCell[1] - colViarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: startCol - areaCell[1]
				});
				mergeCells.push({
					row: areaCell[0] - rowViarable,
					col: startCol - colViarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: areaCell[3] - startCol + 1
				});
			} else {
				mergeCells.push({
					row: areaCell[0] - rowViarable,
					col: areaCell[1] - colViarable,
					rowspan: areaCell[2] - areaCell[0] + 1,
					colspan: areaCell[3] - areaCell[1] + 1
				});
			}
		});
	}

	return mergeCells;
}

// 冻结行列为  选中区域行列号最小的单元格值
function setColumnOfLockParameter(obj, prop, startNum, endNum) {
	if (startNum < endNum) {
		obj[prop] = startNum;
	} else {
		obj[prop] = endNum;
	}
}
