import Utils from '../../../../public/utils';
const { addClassName, showSearchToast, setGlobalStorage, getGlobalStorage } = Utils;
import { getRollingData, sendSearchFun, tableRender, initRollingFilterData } from './index';

export const getInitData = function (res, flag, noDropdownPrint, row) {
	const { ownReportParams } = this.props;

	let resData = res.data;
	if (resData.reportType == 1) {
		let AreaDatas = resData.CellsModel.AreaDatas;
		let fldAreasArray = [];
		AreaDatas.map(item => {
			fldAreasArray = fldAreasArray.concat(item.fldAreas);
		})
		fldAreasArray = fldAreasArray.filter(item => item.fldArea[0][1] == 0)
		this.sortToastText = fldAreasArray[0] && resData.fieldExpCaptionMap[fldAreasArray[0].fldName];
	}
	if (row) showSearchToast(resData, this);
	let drillConditions = resData.drill_query_condition || [], //联查查询条件
		printmenu = resData.printmenu ? resData.printmenu : [], //打印列表
		uSearchMenuArr = []; //联查列表
	if (resData.drillRules && resData.drillRules.length > 0 && resData.drillRules[0] !== null) {
		resData.drillRules.forEach((array) => {
			array.forEach((items) => {
				items.drills.forEach((obj) => {
					obj = Object.assign({}, obj, { fldName: items.fldName });
					uSearchMenuArr.push(obj);
				});
			});
		});
		if (this.props.changeSearchByBusi) {
			uSearchMenuArr = this.props.changeSearchByBusi(uSearchMenuArr);
		}
	}
	this.commonParams = {
		...this.commonParams,
		pk_report: resData.reportId,
		reportId: resData.reportId,
		opents: resData.opents
	};
	if (resData.sideBarSet) {
		this.state.graphicParams = { ...this.state.graphicParams, ...resData.sideBarSet };
	}

	this.initParams = {
		dataRowNum: resData.dataRowNum,
		noDropdownPrint,
		excelPrintMaxRow: resData.excelPrintMaxRow,
		pdfPrintMaxRow: resData.pdfPrintMaxRow,
		key_CUR_USER_ID: resData.key_CUR_USER_ID,
		isShowZero: resData.isShowZero, //当返回为false时，表格区字段是0不显示
		drillConditions,
		printmenu,
		uSearchMenuArr,
		controlByLicense: resData.controlByLicense
	};

	this.pageParams.reportType = resData.reportType;

	let repCondition = resData.repCondition;
	if (repCondition && repCondition.pageInfo && repCondition.pageInfo.length > 0) {
		let status = flag ? 'search' : 'init';
		let repParams = {
			repCondition,
			pagerData: {
				...this.initParams.pagerData,
				type: repCondition.pageType,
				pageInfo: repCondition.pageInfo,
				modalRollingArray: initRollingFilterData.call(this, repCondition.dataArr, repCondition, status)
			}
		};
		this.initParams = Object.assign({}, this.initParams, repParams);
		if (this.initParams.pagerData && this.initParams.pagerData.type != 0) {
			getRollingData.call(this, status);
		}
	}

	if (this.initParams.drillConditions.length !== 0) {
		for (let i = 0; i < drillConditions.length; i++) {
			this.props.search.setSearchValByField(
				this.searchId,
				drillConditions[i].field,
				{ value: drillConditions[i].value },
				drillConditions[i].opertype
			);
		}
	}

	if (flag) {
		setGlobalStorage('sessionStorage', `trans${resData.opents}${resData.reportId}`, resData.transSaveObject || '')
		this.preloadMaxRow = resData.preloadMaxRow;
		this.isConnection = true;
	}
	let LinkReport =
		(ownReportParams && ownReportParams.LinkReport) ||
		getGlobalStorage('localStorage', 'LinkReport') || 
		getGlobalStorage('sessionStorage', 'LinkReport')
	if (LinkReport && !row) {
		let condition = JSON.parse(LinkReport);
		sendSearchFun(this, this.props, this.props, condition, 'simple', {});
	} else {
		tableRender(this, res.data);
	}
	let sortFilterBtns = document.getElementsByClassName('sortAndFilterArea');
	if (sortFilterBtns[0]) {
		for (let i = 0; i < sortFilterBtns.length; i++) {
			addClassName(sortFilterBtns[i]);
		}
	}
	let cancelSortBtn = document.getElementsByClassName('sortDisabled');
	if (cancelSortBtn[0]) {
		addClassName(cancelSortBtn[0]);
	}
	let cancelFilterBtn = document.getElementsByClassName('fitletDisabled');
	if (cancelFilterBtn[0]) {
		addClassName(cancelFilterBtn[0]);
	}
};
