import { toast, ajax } from 'nc-lightapp-front';
import Utils from '../../../../public/utils';
const { langCheck } = Utils;
export const shareReport = function (data) {
	// 点击分享function
	let useridArr = [];
	const { pager, operationQueue, fileName } = this.state;
	const { key_CUR_USER_ID } = this.initParams;
	const { condition } = this.pageParams;
	const { allRowCount } = pager;
	let maxrow = this.preloadMaxRow != -1 && this.preloadMaxRow <= allRowCount ? '-1' : allRowCount;
	let { title, content, sendmethod, userids } = data;
	if (data == 'print' && !title) {
		title = fileName;
		sendmethod = this.outputType;
		content = langCheck('reportMultiLang', '100301-000010'); /* 国际化处理： 感谢您的下载！*/
		useridArr.push(key_CUR_USER_ID);
	} else {
		if (title == '') {
			toast({ content: langCheck('reportMultiLang', '100301-000011'), color: 'warning' }); /* 国际化处理： 请输入消息标题*/
			return;
		}
		if (userids === null || userids.length < 1) {
			toast({ content: langCheck('reportMultiLang', '100301-000012'), color: 'warning' }); /* 国际化处理： 请选择用户*/
			return;
		}

		userids.forEach((item) => {
			useridArr.push(item.refpk);
		});
	}

	let shareObj = {
		title,
		content,
		sendmethod,
		userids: useridArr,
		fileName,
		maxrow,
		operationQueue,
		pageInfo: {
			pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
			pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
		},
		querycondition: condition
	};
	shareObj = Object.assign({}, shareObj, this.commonParams);

	ajax({
		url: '/nccloud/report/widget/lightreportshareaction.do',
		data: shareObj,
		success: (res) => {
			if (res.success) {
				let toastHTML = '';
				toastHTML =
					res.data == '1'
						? langCheck('reportMultiLang', '100301-000093')
						: langCheck('reportMultiLang', '100301-000094');
				toast({ content: toastHTML, color: 'success' });
				this.refs.shareModal.closeShareModal();
				this.refs.confirmModal.setState({
					showConfirmModal: false
				});
			}
		}
	});
};

export const shareMenuClick = function (info) {
	let iconDOM = document.getElementById('fenxiang').getElementsByTagName('i')[0];
	iconDOM.classList.remove('icon-hangcaozuoxiangshang1');
	iconDOM.classList.remove('hangcaozuoxiangshang1');
	iconDOM.classList.add('icon-hangcaozuoxiala1');
	iconDOM.classList.add('hangcaozuoxiala1');

	if (info.key == 'time') {
		let maxrow = this.preloadMaxRow != -1 && this.preloadMaxRow <= this.state.pager.allRowCount ? '-1' : this.state.pager.allRowCount;
		ajax({
			url: '/nccloud/report/widget/timingsharequeryaction.do',
			data: {
				pk_report: this.commonParams.pk_report
			},
			success: res => {
				let { taskList } = res.data;
				this.setState({
					timingList: taskList || [],
					showTimeModal: true,
					maxrow
				})
			}
		})
		return;
	}
	//分享明细查询
	const data = {
		pk_report: this.commonParams.pk_report,
		appcode: this.commonParams.appcode
	};
	ajax({
		url: '/nccloud/report/widget/querysharetask.do',
		data,
		success: (res) => {
			if (res.success) {
				const ShareDetailData = res.data;
				if (ShareDetailData.length < 1) {
					toast({
						content: langCheck('reportMultiLang', '100301-000021'),
						color: 'warning'
					}); /* 国际化处理： 暂无分享的状态更新。*/
					return;
				}
				this.setState({
					ShareDetailData
				});
				this.refs.shareDetailModal.setState({
					showShareDetailModal: true
				});
			}
		}
	});
};
