import { ajax, toast } from 'nc-lightapp-front';
import { isSearchRender } from './isSearchRender';
import Utils from '../../../../public/utils';
const { langCheck, removeGlobalStorage, getGlobalStorage } = Utils;
export default function sendSearchFun(
	that,
	expandProps,
	props,
	condition,
	type,
	queryInfo,
	custcondition,
	showCount,
	isFromDrill
) {
	if (that.state.settings.fixedRowsTop > 0) {
		that.refs.hansontable.refs.hot.hotInstance.scrollViewportTo(that.state.settings.fixedRowsTop, 0)
	} else {
		that.refs.hansontable.refs.hot.hotInstance.scrollViewportTo(0, 0)
	}
	// 单据联查时需要的参数
	let userdefObj = getGlobalStorage('localStorage', 'LinkReportUserDefObj')
		? JSON.parse(getGlobalStorage('localStorage', 'LinkReportUserDefObj'))
		: getGlobalStorage('sessionStorage', 'LinkReportUserDefObj')
			? JSON.parse(getGlobalStorage('sessionStorage', 'LinkReportUserDefObj'))
			: [];
	let sessionCustcondition = getGlobalStorage('localStorage', 'sessionCustcondition')
		? JSON.parse(getGlobalStorage('localStorage', 'sessionCustcondition'))
		: getGlobalStorage('sessionStorage', 'sessionCustcondition')
			? JSON.parse(getGlobalStorage('sessionStorage', 'sessionCustcondition'))
			: false;
	removeGlobalStorage('localStorage', 'LinkReportUserDefObj')
	removeGlobalStorage('sessionStorage', 'LinkReportUserDefObj')
	removeGlobalStorage('localStorage', 'sessionCustcondition')
	removeGlobalStorage('sessionStorage', 'sessionCustcondition')
	const { pager, operationQueue } = that.state;
	let { pageSize, pageIndex } = pager;
	if (pageSize < 10) {
		pageSize = 10;
		toast({
			content: langCheck('reportMultiLang', '100301-000017'),
			color: 'warning'
		}); /* 国际化处理： 每页行数应在10—2000之间。*/
	}
	if (pageSize > 2000) {
		pageSize = 2000;
		toast({
			content: langCheck('reportMultiLang', '100301-000017'),
			color: 'warning'
		}); /* 国际化处理： 每页行数应在10—2000之间。*/
	}
	const { pk_report, reportId, queryAreaCode, oid, opents, appcode, pageCode, querytype } = that.commonParams;
	if (that.props.ownReportParams && that.props.ownReportParams.userdefObj)
		userdefObj = that.props.ownReportParams.userdefObj;
	let isAdvanced = type === 'super' ? true : false;
	that.commonParams.isAdvanced = isAdvanced;
	let requireTotalData = showCount == 'showAllCount' ? 'true' : 'false'; //分页中是否显示真实行数

	let dataParam = {
		pk_report,
		reportId,
		queryAreaCode,
		oid,
		opents,
		appcode,
		pageCode,
		operationQueue: operationQueue || '',
		pageInfo: {
			pageIndex: (pageIndex && pageIndex.toString()) || '',
			pageSize: (pageSize && pageSize.toString()) || ''
		},
		querytype,
		querycondition: condition,
		isAdvanced,
		key_time_offset: that.key_time_offset,
		userdefObj,
		custcondition: sessionCustcondition || custcondition || {},
		requireTotalData,
		controlByLicense: that.initParams.controlByLicense
	};
	if (that.props.isSearchFromDesign) {
		dataParam = Object.assign({}, dataParam, { smartba_search: true });
	}
	that.sendSearchParams = dataParam;
	let url, data, diffConditon;
	if (isFromDrill) {
		url = '/nccloud/report/widget/lightreport_reportdrill.do';
		data = that.isReportDrillParams;
		data.requireTotalData = requireTotalData;
		diffConditon = that.isReportDrillParams.querycondition;
		that.commonParams.keyIsExecDrill = true;
	} else {
		url = '/nccloud/report/widget/lightreport_querydata.do';
		data = dataParam;
		diffConditon = condition;
		that.commonParams.keyIsExecDrill = false;
	}
	ajax({
		url,
		data,
		success: (res) => {
			isSearchRender.call(that, res, diffConditon, queryInfo);
		}
	});
}
