import { ajax, toast } from 'nc-lightapp-front';
import { tableRender } from './methods';
import Utils from '../../../../public/utils';
const { langCheck } = Utils;

export const sendRollingData = function(req) {
	// 发送页维度请求
	let reqArray = [];
	let { pageInfo } = this.initParams.repCondition;
	const { operationQueue, pager } = this.state;
	const { condition } = this.pageParams;
	const {
		pk_report,
		queryAreaCode,
		oid,
		transSaveObject,
		opents,
		appcode,
		pageCode,
		querytype,
		isAdvanced
	} = this.commonParams;
	req.forEach((item) => {
		pageInfo.forEach((ele) => {
			if (item.id == ele.id) {
				reqArray.push({
					disValue: item.disValue,
					relValue: item.relValue,
					disCode: ele.disCode,
					relCode: ele.relCode
				});
			}
		});
	});
	let data = {
		pk_report,
		queryAreaCode,
		oid,
		pageInfoForDim: reqArray,
		operationQueue,
		transSaveObject,
		opents,
		appcode,
		pageCode,
		querytype,
		pageInfo: {
			pageIndex: '0',
			pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
		},
		querycondition: condition,
		isAdvanced
	};
	this.commonParams.pageInfoForDim = reqArray;
	ajax({
		url: '/nccloud/report/widget/lightreport_reportpageDim.do',
		data,
		success: (res) => {
			if (res.success) {
				this.sortStyle.headerSwitch = null;
				this.filterStyle.filterColIndexArray = [];
				this.sortStyle.sortStyles = [];
				tableRender(this, res.data);
			}
		}
	});
};

export const initRollingFilterData = function(dataArr, repCondition, way) {
	// 筛选模态框数据
	let arrayObj = dataArr[0];
	let newArray = [];
	let modalArray = [];
	if (!arrayObj && way == 'init') {
		for (let i = 0; i < repCondition.pageInfo.length; i++) {
			newArray.push({ perch: i }); //占位
			repCondition.pageInfo[i].selectedDisValue = ' ';
		}
		return newArray;
	}
	if (way == 'search') {
		arrayObj && arrayObj.map((item, index) => {
			newArray[index] = [];
			if (repCondition.pageInfo[index].isShowAll) {
				newArray[index].push({
					id: index + 1,
					relValue: '%$S(*@#^)',
					disValue: langCheck('reportMultiLang', '100301-000018') /* 国际化处理： 显示全部*/,
					parentField: 'xianshiquanbupid',
					rootField: 'xianshiquanbu'
				});
			} else {
				newArray[index].push({
					id: index + 1,
					relValue: repCondition.pageInfo[index].selectedRelValue || ' ',
					disValue: repCondition.pageInfo[index].selectedDisValue || ' '
				});
			}
		});
	}

	dataArr.map((itemArray, i) => {
		itemArray.map((itemObjec, index) => {
			newArray[index].push(itemObjec);
		});
	});
	let isrelValueNull = true;
	newArray.forEach((items, index) => {
		//去重
		modalArray[index] = [];
		items.forEach((item) => {
			let flag = true;
			for (let i = 0; i < modalArray[index].length; i++) {
				if (
					(item.disValue == modalArray[index][i].disValue &&
						item.relValue == modalArray[index][i].relValue) ||
					item.disValue == 'null'
				) {
					flag = false;
				}
				if (item.relValue == 'null') {
					flag = false;
					isrelValueNull = false;
				}
			}
			if (flag) {
				modalArray[index].push(item);
			}
		});
		if (!isrelValueNull) {
			modalArray[index].push({
				id: index + 1,
				relValue: null,
				disValue: langCheck('reportMultiLang', '100301-000019')
			}); /* 国际化处理： 显示空行*/
		}
	});
	return modalArray;
};

export const getRollingData = function(way) {
	// 上下页页维度数据
	const { repCondition, pagerData } = this.initParams;
	let { dataArr, pageInfo } = repCondition;
	let { count } = pagerData;
	let res;
	let upDownRollingArray = [];
	if (way == 'search' && way != 'init' && pageInfo[0].isShowAll) {
		let showAllArray = [];
		pageInfo.forEach((item, index) => {
			showAllArray[index] = {
				id: index + 1,
				relValue: '%$S(*@#^)',
				disValue: langCheck('reportMultiLang', '100301-000018') /* 国际化处理： 显示全部*/,
				parentField: 'xianshiquanbupid',
				rootField: 'xianshiquanbu'
			};
		});
		dataArr.unshift(showAllArray);
	}
	if (dataArr.length < 1) {
		dataArr.length = pageInfo.length;
		for (let i = 0; i < dataArr.length; i++) {
			upDownRollingArray.push({ perch: i });
		}
		this.initParams = {
			...this.initParams,
			pagerData: {
				...this.initParams.pagerData,
				upDownRollingArray,
				flag: false
			}
		};
	} else {
		res = dataArr[count];
		res.forEach((item) => {
			upDownRollingArray.push(item);
		});
		this.initParams = {
			...this.initParams,
			pagerData: {
				...this.initParams.pagerData,
				upDownRollingArray,
				flag: true
			}
		};
		if (way != 'search' && way != 'init') {
			sendRollingData.call(this, upDownRollingArray);
		}
	}
};

export const rollingFilterClick = function() {
	// 点击页维度筛选按钮
	let rollingPagerData = this.state.rollingPagerData;
	if (rollingPagerData.length < 1) {
		return;
	}
	for (let i = 0; i < rollingPagerData.length; i++) {
		if (rollingPagerData[i] == undefined) {
			rollingPagerData[i] = this.initParams.pagerData.modalRollingArray[i][0];
		}
	}
	this.state.rollingPagerData = rollingPagerData;
	sendRollingData.call(this, rollingPagerData);
};

export const showPagerClick = function(index, data, pageInfo) {
	// 点击页维度操作
	if (!data[0][0]) {
		toast({ content: langCheck('reportMultiLang', '100301-000020'), color: 'warning' }); /* 国际化处理： 请先进行查询。*/
		return;
	}
	this.setState({
		pagerModalData: {
			data,
			index,
			pageInfo,
			showPagerModal: true
		}
	});
};

export const rollingUpDown = function(way, subscript) {
	// 点击上下页
	let { count } = this.initParams.pagerData;
	let { dataArr } = this.initParams.repCondition;
	if (way === 'prev') {
		count--;
		if (count < 0) {
			count = dataArr.length - 1;
		}
	} else if (way === 'next') {
		count++;
		if (count > dataArr.length - 1) {
			count = 0;
		}
	} else if (way === 'first') {
		count = 0;
	} else if (way === 'last') {
		count = dataArr.length - 1;
	} else if (way === 'onlyUpDown') {
		count = subscript;
	}
	this.initParams.pagerData.count = count;
	this.state.pagerModalData.showPagerModal = false;
	getRollingData.call(this);
};
