import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCModal, NCTable, NCButton, NCTree } = base;
const TreeNode = NCTree.NCTreeNode;
import Utils from '../utils';
const { buildTree, langCheck } = Utils;
require('./index.less');

export default class PagerModal extends Component {
	constructor(props) {
		super(props);
		this.selectData = {};
	}

	componentWillReceiveProps = (newProps) => {
		let result = JSON.parse(JSON.stringify(newProps));
		if (newProps.pagerModalData.data !== null && newProps.pagerModalData.showPagerModal) {
			this.treeData = this.createTreeData(result);
		}
	};

	createTreeData = (result) => {
		const { pagerModalData } = result;
		const { data, index, pageInfo } = pagerModalData;
		let treeData = JSON.parse(JSON.stringify(data[index]));
		if (pageInfo[index].treeStyle == 1 || pageInfo[index].treeStyle == 2) {
			treeData = buildTree(treeData, 'rootField', 'parentField');
		}
		return treeData;
	};

	onSelectTree = (index, selectedKeys, e) => {
		let obj = {
			id: index + 1,
			relValue: selectedKeys[0],
			disValue: e.node.props.title
		};
		this.props.checkPaper(obj, index, 'noSelect', e.node.props['data-order']);
	};

	render() {
		const treeData = this.treeData;
		const { data, index, pageInfo, showPagerModal } = this.props.pagerModalData;
		let canClick = false;
		if (pageInfo && pageInfo.length > 1) {
			canClick = true
		}
		const columns = [
			{
				title: langCheck('reportMultiLang', '100301-000060'),/* 国际化处理： 显示值*/
				dataIndex: 'disValue',
				key: 'disValue'
			}
			// {
			// 	title: '隐藏值',
			// 	dataIndex: 'relValue',
			// 	key: 'relValue',
			// 	width: 50
			// }
		];
		const loop = (treeData) =>
			treeData.map((item) => {
				if (item.children) {
					return (
						<TreeNode
							key={item.relValue}
							title={item.disValue}
							data-index={index}
							data-order={item.orderIndex}
						>
							{loop(item.children)}
						</TreeNode>
					);
				}
				return <TreeNode key={item.relValue} title={item.disValue} data-order={item.orderIndex} />;
			});
		return (
			<NCModal show={showPagerModal} id="pagerModal" className="report-modal-class">
				<NCModal.Header>
					<NCModal.Title fieldid={langCheck('reportMultiLang', '100301-000061')}>{langCheck('reportMultiLang', '100301-000061')}</NCModal.Title>{/* 国际化处理： 页维度筛选*/}
				</NCModal.Header>
				{/*
					treeStyle: 0 => 表格形式显示
					treeStyle: 1 => 自定义格式显示，例 2-2-2
					treeStyle: 2 => 父子关系显示
 				*/}
				<NCModal.Body>
					{pageInfo !== undefined &&
						pageInfo[index].treeStyle == 0 && (
							<NCTable
								onRowDoubleClick={(record, i, indent) => {
									this.props.checkPaper(record, index, 'noSelect', i, canClick);
								}}
								onRowClick={(record, i, indent) => {
									this.selectData = {
										record,
										index,
										i
									}
								}}
								columns={columns}
								data={data != null && data[index]}
								scroll={{ y: 255 }}
							/>
						)}
					{pageInfo !== undefined &&
						(pageInfo[index].treeStyle == 2 || pageInfo[index].treeStyle == 1) && (
							<div>
								<NCTree showLine={true} onSelect={this.onSelectTree.bind(this, index)}>
									{loop(treeData)}
								</NCTree>
							</div>
						)}
				</NCModal.Body>
				<NCModal.Footer>
					<NCButton colors="primary" className="sure-button" onClick={() => {
						if (this.selectData.record) this.props.checkPaper(this.selectData.record, this.selectData.index, 'noSelect', this.selectData.i, canClick)
					}}>
						{langCheck('reportMultiLang', '100301-000059')}
						{/* 国际化处理： 确定*/}
					</NCButton>
					<NCButton className="cancel-button" onClick={this.props.closePagerModal} shape="border">
						{langCheck('reportMultiLang', '100301-000048')}{/* 国际化处理： 取消*/}
					</NCButton>
				</NCModal.Footer>
			</NCModal>
		);
	}
}
