import { ajax, toast } from 'nc-lightapp-front';
import { tableRender } from './methods';
import Utils from '../../../../public/utils';
const { langCheck } = Utils;

export default function getPagerData(way) {
	const { operationQueue, pager } = this.state;
	const { noDropdownPrint } = this.initParams;
	const { condition } = this.pageParams;
	if (noDropdownPrint) {
		return;
	}
	let { pageSize, pageIndex, allRowCount } = pager;
	if (pageSize < 10) {
		this.state.pager.pageSize = 10;
		toast({
			content: langCheck('reportMultiLang', '100301-000017'),
			color: 'warning'
		}); /* 国际化处理： 每页行数应在10—2000之间。*/
	}
	if (pageSize > 2000) {
		this.state.pager.pageSize = 2000;
		toast({
			content: langCheck('reportMultiLang', '100301-000017'),
			color: 'warning'
		}); /* 国际化处理： 每页行数应在10—2000之间。*/
	}
	pageIndex = way == 'ensure' ? 1 : pageIndex;
	let data = {
		allRowCount: allRowCount.toString(),
		operationQueue: operationQueue,
		pageInfo: {
			pageIndex: (pageIndex && pageIndex.toString()) || '',
			pageSize: (this.state.pager.pageSize && this.state.pager.pageSize.toString()) || ''
		},
		querycondition: condition
	};
	data = Object.assign({}, data, this.commonParams);
	ajax({
		url: '/nccloud/report/widget/lightreport_turnpage.do',
		data,
		success: (res) => {
			if (res.success) {
				this.sendSearchParams.pageInfo.pageIndex = pageIndex.toString();
				this.sendSearchParams.pageInfo.pageSize = this.state.pager.pageSize.toString();
				tableRender(this, res.data);
				document.getElementById('totalSumNum').innerHTML = '';
				document.getElementById('totalCountNum').innerHTML = '';
			}
		}
	});
}
