import Utils from '../../../../public/utils'
const { addClassName, removeClassName, outPutTotalNum } = Utils
export default function afterSelection (obj, r, c, r2, c2) {
  let row = r,
    row2 = r2,
    col = c,
    col2 = c2
  if (r > r2) {
    row = r2
    row2 = r
  }
  if (c > c2) {
    col = c2
    col2 = c
  }
  this.coords.row = row
  this.coords.col = col
  // 求和计数
  let { area, fldAreaArray, settings, pager } = this.state
  let { filterColIndexArray } = this.filterStyle
  let { sortStyles } = this.sortStyle
  let { headerSwitch } = this.sortStyle
  const { noDropdownPrint } = this.initParams
  let sortAndFilterArea = false
  let fitletDisabled = true
  let sortDisabled = true

  if (r <= area[2] && c <= area[3]) {
    this.isRange = false
  } else {
    this.isRange = true
  }
  if (fldAreaArray[c]) {
    for (let i = 0; i < fldAreaArray[c].length; i++) {
      if (r <= fldAreaArray[c][i].endRow && r >= fldAreaArray[c][i].startRow) {
        sortAndFilterArea = true
        break
      }
    }
  }

  for (let i = 0; i < filterColIndexArray.length; i++) {
    if (c == filterColIndexArray[i]) {
      fitletDisabled = false
      break
    }
  }

  for (let i = 0; i < sortStyles.length; i++) {
    if (c == sortStyles[i].index) {
      sortDisabled = false
      break
    }
  }

  if (c == headerSwitch) {
    sortDisabled = false
  }

  /**
   * 控制按钮如果操作state会存在性能问题，现直接操作DOM
   */
  // sortAndFilterArea 控制升降序，筛选，冻结，联查
  let sortFilterBtns = document.getElementsByClassName('sortAndFilterArea')
  if (sortAndFilterArea && !noDropdownPrint) {
    if (sortFilterBtns[0]) {
      if (!this.emptyDataFlag) {
        for (let i = 0; i < sortFilterBtns.length; i++) {
          removeClassName(sortFilterBtns[i])
        }
      } else {
        for (let i = 0; i < sortFilterBtns.length; i++) {
          addClassName(sortFilterBtns[i])
        }
      }
      // if (pager && pager.allRowCount == 0 && sortFilterBtns[3]) { //暂时先注释，后续会修改
      //   addClassName(sortFilterBtns[3])
      // }
    }
  } else {
    if (sortFilterBtns[0]) {
      for (let i = 0; i < sortFilterBtns.length; i++) {
        addClassName(sortFilterBtns[i])
      }
    }
  }

  // sortDisabled && sortAndFilterArea 控制取消排序
  let cancelSortBtn = document.getElementsByClassName('sortDisabled')
  let cancelFilterBtn = document.getElementsByClassName('fitletDisabled')
  if (cancelSortBtn[0]) {
    if (sortAndFilterArea && !sortDisabled && !this.emptyDataFlag) {
      removeClassName(cancelSortBtn[0])
    } else {
      addClassName(cancelSortBtn[0])
    }
  }

  if (cancelFilterBtn[0]) {
    if (sortAndFilterArea && !fitletDisabled && !this.emptyDataFlag) {
      removeClassName(cancelFilterBtn[0])
    } else {
      addClassName(cancelFilterBtn[0])
    }
  }
  this.sortAndFilterArea = sortAndFilterArea
  if (document.getElementById('totalSumNum')) {
    if (row == row2 && col == col2) {
      document.getElementById('totalSumNum').innerHTML = ''
      document.getElementById('totalCountNum').innerHTML = ''
      return
		}
    let { mergeCells } = settings

    let content = [],
      dataArray = []
    for (let i = row; i <= row2; i++) {
      for (let j = col; j <= col2; j++) {
        content.push({ row: i, col: j })
      }
    }
    for (let i = 0; i < content.length; i++) {
      for (let j = 0; j < mergeCells.length; j++) {
        let row1, row2, col1, col2
        row1 = mergeCells[j].row
        row2 = row1 + mergeCells[j].rowspan
        col1 = mergeCells[j].col
        col2 = col1 + mergeCells[j].colspan
        if (
          content[i].row >= row1 &&
          content[i].row < row2 &&
          content[i].col >= col1 &&
          content[i].col < col2 &&
          !(content[i].row === row1 && content[i].col === col1)
        ) {
          dataArray.push(content[i])
        }
      }
    }

    dataArray.forEach((item) => {
      content.forEach((ele, index) => {
        if (item.row == ele.row && item.col == ele.col) {
          content.splice(index, 1)
        }
      })
    })
    let sumArr = []
    content.forEach((item) => {
      sumArr.push(obj.hotInstance.getDataAtCell(item.row, item.col))
    })

    document.getElementById('totalSumNum').innerHTML = outPutTotalNum(sumArr) || ''
    document.getElementById('totalCountNum').innerHTML = content.length > 1 ? content.length : ''
  }
}
