import { toast, ajax, formDownload } from 'nc-lightapp-front';
import Utils from '../../../../public/utils';
const { langCheck } = Utils;

export const commomPrint = function (info, way) {
	// 打印导出
	let iconDOM = document.getElementById('dayin').getElementsByTagName('i')[0];
	iconDOM.classList.remove('icon-hangcaozuoxiangshang1');
	iconDOM.classList.remove('hangcaozuoxiangshang1');
	iconDOM.classList.add('icon-hangcaozuoxiala1');
	iconDOM.classList.add('hangcaozuoxiala1');
	let { pager, operationQueue, fileName } = this.state;
	const { excelPrintMaxRow, pdfPrintMaxRow } = this.initParams;
	const { FreeReport_QueryTreeVo } = this.pageParams;
	const { allRowCount } = pager;
	let maxrow = this.preloadMaxRow != -1 && this.preloadMaxRow <= allRowCount ? '-1' : allRowCount;
	let printExpend = info.item && info.item.props && info.item.props.printExpend;
	let fileSuffix = way == 'noWay' ? 'pdf' : info.key;
	if (printExpend) {
		fileSuffix = 'pdf';
	}
	let printData = {
		fileName,
		fileSuffix,
		operationQueue,
		maxrow: maxrow || '1',
		FreeReport_QueryTreeVo
	};
	if (printExpend) {
		printData = Object.assign({}, printData, this.commonParams, { key: info.key });
		this.props.printExpendFun(printData);
		return;
	}
	if (info.key == 'xlsx' && allRowCount > excelPrintMaxRow) {
		this.showConfirm();
		this.outputType = '0';
		return;
	} else {
		if (Number(allRowCount) > Number(pdfPrintMaxRow) && (info.key == 'pdf' || way == 'noWay')) {
			this.showConfirm();
			this.outputType = '1';
			return;
		}
	}
	if (way !== 'noWay') {
		ajax({
			url: '/nccloud/report/widget/lightreportprintauthoritycheck.do',
			data: {},
			success: res => {
				if (info.key !== 'xlsx' && info.key !== 'pdf' && info.key !== 'csv') {
					this.setState({
						nodekey: info.key
					});
					// 打印
					ajax({
						url: '/nccloud/report/widget/lightreportgetpidaction.do',
						data: {
							nodekey: info.key,
							appcode: this.commonParams.appcode,
							FreeReport_QueryTreeVo
						},
						success: (res) => {
							if (res.success) {
								let printModalArray = res.data;
								if (printModalArray.length < 1) {
									toast({
										content: langCheck('reportMultiLang', '100301-000014'),
										color: 'warning'
									}); /* 国际化处理： 无可用的打印模板。*/
									return;
								} else if (printModalArray.length == 1) {
									this.setState(
										{
											nodekey: info.key
										},
										() => templatePrint.call(this, printModalArray[0].templateid)
									);
								} else {
									this.setState({
										outputModalData: printModalArray
									});
									this.refs.outputModal.setState({
										showOutputModal: true
									});
								}
							}
						}
					});
				} else {
					printData = Object.assign({}, printData, this.commonParams);
					// 导出
					setTimeout(() => {
						formDownload({
							params: printData,
							url: '/nccloud/report/widget/lightreport_ajaxexport.do',
							enctype: 2
						});
					}, 50);
				}
			}
		})
	} else {
		printData = Object.assign({}, printData, this.commonParams);
		// 打印按钮
		setTimeout(() => {
			formDownload({
				params: printData,
				url: '/nccloud/report/widget/printbuttonaction.do',
				enctype: 2
			});
		}, 50);
	}
};

export const templatePrint = function (type) {
	const { fileName, pager, nodekey, operationQueue } = this.state;
	const { FreeReport_QueryTreeVo, condition } = this.pageParams;
	const { printUrlChange } = this.props;
	let userjson = {
		operationQueue,
		pageInfo: {
			pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
			pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
		},
		querycondition: condition,
		FreeReport_QueryTreeVo
	};
	userjson = Object.assign({}, userjson, this.commonParams);
	const outputJson = {
		filename: fileName,
		funcode: this.commonParams.appcode,
		nodekey: nodekey || '',
		oids: this.commonParams.oid ? [this.commonParams.oid] : [],
		printTemplateID: type,
		userjson: JSON.stringify(userjson)
	};
	let urlPdf = '/nccloud/report/widget/printreportbypdfaction.do';
	if (printUrlChange) {
		if (printUrlChange(nodekey) === false || printUrlChange(nodekey) === '') {
			urlPdf = '/nccloud/report/widget/printreportbypdfaction.do';
		} else {
			urlPdf = printUrlChange(nodekey);
		}
	}
	formDownload({
		params: outputJson,
		url: urlPdf,
		enctype: 2
	});
};
