import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCModal, NCTable, NCButton } = base;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class ShareDetailModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showShareDetailModal: false
		};
	}

	closeShareDetailModal() {
		this.setState({
			showShareDetailModal: false
		});
	}

	render() {
		const { data } = this.props;
		const columns = [
			{
				title: langCheck('reportMultiLang', '100301-000070'),/* 国际化处理： 序号*/
				dataIndex: 'jobId',
				key: 'jobId',
				width: 50
			},
			{
				title: langCheck('reportMultiLang', '100301-000071'),/* 国际化处理： 消息标题*/
				dataIndex: 'subject',
				key: 'subject',
				width: 150
			},
			{
				title: langCheck('reportMultiLang', '100301-000082'),/* 国际化处理： 创建时间*/
				dataIndex: 'createTime',
				key: 'createTime',
				width: 150
			},
			{
				title: langCheck('reportMultiLang', '100301-000072'),/* 国际化处理： 状态*/
				dataIndex: 'status',
				key: 'status',
				width: 150,
				render: (text, record, index) => {
					let html = '';
					switch (text) {
						case 6:
							html = langCheck('reportMultiLang', '100301-000074');/* 国际化处理： 正在执行*/
							break;
						case 7:
							html = langCheck('reportMultiLang', '100301-000076');/* 国际化处理： 任务完成*/
							break;
						case 9:
							html = langCheck('reportMultiLang', '100301-000077');/* 国际化处理： 执行出错*/
							break;
					}
					return <span>{html}</span>;
				}
			}
		];
		return (
			<NCModal show={this.state.showShareDetailModal} id="shareDetailModal" className="report-modal-class">
				<NCModal.Header>
					<NCModal.Title fieldid={langCheck('reportMultiLang', '100301-000028')}>{langCheck('reportMultiLang', '100301-000028')}</NCModal.Title>{/* 国际化处理： 分享状态*/}
				</NCModal.Header>

				<NCModal.Body>
					<NCTable
						columns={columns}
						data={data}
						scroll={{ y: 255 }}
					/>
				</NCModal.Body>
				<NCModal.Footer>
					<NCButton className="cancel-button" onClick={this.closeShareDetailModal.bind(this)} shape="border">
						{langCheck('reportMultiLang', '100301-000048')}{/* 国际化处理： 取消*/}
					</NCButton>
				</NCModal.Footer>
			</NCModal>
		);
	}
}
