import React, { Component } from 'react';
import { createPage, base, ajax, high, toast, pageTo, getMultiLang } from 'nc-lightapp-front';
const { NCButton, NCModal, NCPopconfirm } = base;
import TaskForm from './form';
import TimeChooseModel from './timeModal';
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

class TimeModal extends Component {
  constructor(props) {
    super(props);
    this.prevPageTask = ''; //记录一下上一个点击的节点，取消时用
    this.pk_timingshare_task = ''; //对应的点击id
    this.appName = pageTo.getUrlParam('n') || pageTo.getSearchParam('n') || ''
    this.state = {
      timingList: [], //左侧列表
      status: 'preview', //编辑态：'edit'  浏览态：''
      operType: 'add',
      pageData: {
        taskName: '', //任务名称
        taskNote: '', //说明
        timeConfig: {}, //定时设置
        receivers: [], //接收人
        fileType: '0', //附件类型
        taskExecRoom: {
          isCurUser: '0', //用户 //当前用户0 or 接收人1
          exectimezone: {}, //时区
          execlanguage: {} //语种
        },
        msgTitle: '', //消息标题
        msgVars: [], //消息标题下面的checkbox
        msgText: '', //消息正文
      },
      copyPageData: {
        taskName: '', //任务名称
        taskNote: '', //说明
        timeConfig: {}, //定时设置
        receivers: [], //接收人
        fileType: '0', //附件类型
        taskExecRoom: {
          isCurUser: '0', //用户 //当前用户0 or 接收人1
          exectimezone: {}, //时区
          execlanguage: {} //语种
        },
        msgTitle: '', //消息标题
        msgVars: [], //消息标题下面的checkbox
        msgText: '', //消息正文
      }, //副本，取消时使用
    }
  }

  componentWillMount() {
    let callback = (json) => {
      window.reportTimeMultiLang = json;
    };
    getMultiLang({ moduleId: 100303, currentLocale: 'zh-CN', domainName: 'lappreportrt', callback });
  }

  componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.show) {
      this.emptyFun();
      if (nextProps.timingList.length > 0) {
        let firstItem = nextProps.timingList[0];
        this.pk_timingshare_task = firstItem.pk_timingshare_task;
        this.state.timingList = nextProps.timingList;
        this.state.status = 'preview';
        this.getPageData();
      } else {
        this.setState({
          status: 'edit'
        })
      }
    }
  }

  getPageData = () => {
    //获取右侧数据
    ajax({
      url: '/nccloud/report/widget/timingsharequerydataaction.do',
      data: {
        pk_timingshare_task: this.pk_timingshare_task || this.prevPageTask
      },
      success: (res) => {
        res.data.msgVars = res.data && res.data.msgVars && res.data.msgVars.split(',');
        this.setState({
          pageData: {
            //...this.state.pageData,
            ...res.data
          }
        })
      }
    })
  }

  getListData = () => {
    //获取列表数据
    ajax({
      url: '/nccloud/report/widget/timingsharequeryaction.do',
      data: {
        pk_report: this.props.commonParams.pk_report
      },
      success: res => {
        let { taskList } = res.data;
        this.setState({
          timingList: taskList || []
        })
      }
    })
  }

  emptyFun = () => {
    //清空pageData
    this.state.pageData.taskName = '';
    this.state.pageData.taskNote = '';
    this.state.pageData.timeConfig = {};
    this.state.pageData.receivers = [];
    this.state.pageData.fileType = '0';
    this.state.pageData.msgTitle = '';
    this.state.pageData.msgVars = [];
    this.state.pageData.msgText = '';
    this.state.pageData.taskExecRoom.isCurUser = '0';
    this.state.pageData.taskExecRoom.exectimezone = {};
    this.state.pageData.taskExecRoom.execlanguage = {};
    this.refs.timeChooseModal && this.refs.timeChooseModal.setTimeDataNull();
  }

  onSaveAndAdd = () => {
    //确定并新增
    this.saveData('saveAndAdd');
  }

  onSaveInfo = () => {
    //确定
    this.saveData('save');
  }

  saveData = (way) => {
    //保存逻辑
    const { operType, pageData } = this.state;
    if (pageData.taskName == '') {
      toast({ content: langCheck('reportTimeMultiLang', '100303-000040'), color: 'danger' });
      return;
    }
    if (!pageData.timeConfig.occurType) {
      toast({ content: langCheck('reportTimeMultiLang', '100303-000041'), color: 'danger' });
      return;
    }
    if (pageData.receivers.length < 1) {
      toast({ content: langCheck('reportTimeMultiLang', '100303-000042'), color: 'danger' });
      return;
    }
    let pageParams = Object.assign({}, pageData)
    pageParams.receivers = [];

    pageParams.msgVars = pageData.msgVars && pageData.msgVars.join(',')
    pageData.receivers.map(item => {
      pageParams.receivers.push({ refname: item.refname, refpk: item.refpk })
    })
    if (pageParams.taskExecRoom.exectimezone && pageParams.taskExecRoom.exectimezone.values) delete pageParams.taskExecRoom.exectimezone.values;
    if (pageParams.taskExecRoom.execlanguage && pageParams.taskExecRoom.execlanguage.values) delete pageParams.taskExecRoom.execlanguage.values;
    let data = {
      ...this.props.commonParams,
      operationQueue: this.props.operationQueue,
      querycondition: this.props.querycondition,
      appName: this.appName,
      operType,
      maxrow: this.props.maxrow,
      ...pageParams
    }
    ajax({
      url: '/nccloud/report/widget/timingshareaction.do',
      data,
      success: (res) => {
        toast({ content: langCheck('reportTimeMultiLang', '100303-000043'), color: 'success' });
        if (way == 'save') {
          this.pk_timingshare_task = res.data.pk_timingshare_task
          this.state.status = 'preview';
        } else {
          this.pk_timingshare_task = '';
          this.prevPageTask = res.data.pk_timingshare_task;
          this.state.operType = 'add';
          this.emptyFun();
        }
        this.getListData();
      }
    });
  }

  onCancel = () => {
    //取消
    if (this.state.timingList.length < 1) {
      if (JSON.stringify(this.state.copyPageData) == JSON.stringify(this.state.pageData)) {
        this.onHide();
        return;
      }
      this.setState({
        pageData: {
          ...this.state.copyPageData
        }
      })
      return;
    }
    this.state.operType = 'update';
    this.state.status = 'preview';
    this.getPageData();
  }

  addTask = () => {
    //新增任务
    this.emptyFun();
    this.state.operType = 'add';
    this.setState({
      status: 'edit'
    })
  }

  onEdit = (e, pk_timingshare_task) => {
    //编辑
    e.stopPropagation();
    this.pk_timingshare_task = pk_timingshare_task;
    this.state.status = 'edit';
    this.state.operType = 'update';
    this.getPageData();
  }

  onDelete = (e, pk_timingshare_task) => {
    //删除
    e.stopPropagation();
    ajax({
      url: '/nccloud/report/widget/timingsharedeleteaction.do',
      data: {
        pk_timingshare_task
      },
      success: (res) => {
        toast({ content: langCheck('reportTimeMultiLang', '100303-000044'), color: 'success' });
        this.state.status = 'none';
        this.getListData();
      }
    });
  }

  changeLi = (pk_timingshare_task, index) => {
    //切换右侧数据
    this.prevPageTask = this.pk_timingshare_task;
    if (this.pk_timingshare_task == pk_timingshare_task) return;
    this.state.status = 'preview';
    this.pk_timingshare_task = pk_timingshare_task;
    this.emptyFun();
    this.getPageData()
  }

  onChange = (way, val) => {
    switch (way) {
      case 'isCurUser':
      case 'exectimezone':
      case 'execlanguage':
        this.setState({
          pageData: {
            ...this.state.pageData,
            taskExecRoom: {
              ...this.state.pageData.taskExecRoom,
              [way]: val
            }
          }
        })
        break;
      default:
        this.setState({
          pageData: {
            ...this.state.pageData,
            [way]: val
          }
        })
        break;
    }
  }

  showTimeModal = () => {
    this.refs.timeChooseModal.onOpen(this.state.pageData);
  }

  onTimeModalEnter = (data) => {
    this.setState({
      pageData: {
        ...this.state.pageData,
        timeConfig: data
      }
    })
  }

  onHide = () => {
    this.props.onTimeModalHide()
  }

  render() {
    if (!this.props.show) return null;
    const { DragWidthCom } = this.props
    const { timingList, pageData, status } = this.state;
    let disabledClass = status == 'edit' ? 'disabled' : '';
    return (
      <React.Fragment>
        <NCModal
          size="lg"
          show={this.props.show}
          onHide={this.onHide}
          bodyHeight={status == 'preview' ? '516px' : '472px'}
          className={`time-modal ${status == 'preview' ? 'modal-padding' : ''}`}
        >
          <NCModal.Header closeButton>
            <NCModal.Title fieldid={langCheck('reportTimeMultiLang', '100303-000024')}>{langCheck('reportTimeMultiLang', '100303-000024')}{/* 国际化处理： 定时分享*/}</NCModal.Title>
          </NCModal.Header>
          <NCModal.Body className={status == 'preview' ? 'has-resize-icon nc-theme-common-font-c' : 'nc-theme-common-font-c'}>
            <DragWidthCom
              leftDom={
                <React.Fragment>
                  <h4 className={`add-task ${disabledClass}`} onClick={() => this.addTask()}>{langCheck('reportTimeMultiLang', '100303-000025')}{/* 国际化处理： 新增任务*/}</h4>
                  {timingList.length > 0 &&
                    <ul className="task-list">
                      {timingList.map((item, index) => {
                        return <li className={`${disabledClass} ${this.pk_timingshare_task == item.pk_timingshare_task ? 'active' : ''}`} >
                          <span title={item.task_name} className="task-name" onClick={() => this.changeLi(item.pk_timingshare_task, index)}>{item.task_name}</span>
                          {status != 'edit' && <span className="icon iconfont icon-bianji" onClick={e => this.onEdit(e, item.pk_timingshare_task)}></span>}
                          {status != 'edit' && <NCPopconfirm onClose={e => this.onDelete(e, item.pk_timingshare_task)} rootClose="true" trigger="click" placement="right" content={langCheck('reportTimeMultiLang', '100303-000045')}>{/* 国际化处理： 确定要删除吗？*/}
                            <span className="icon iconfont icon-shanchu"></span>
                          </NCPopconfirm>}
                        </li>
                      })}
                    </ul>
                  }
                </React.Fragment>
              } //左侧区域dom
              rightDom={
                <React.Fragment>
                  {status == 'preview' ?
                    <React.Fragment>
                      <TaskForm status={status} pageData={pageData} onChange={this.onChange}></TaskForm>
                    </React.Fragment>
                    : status == 'edit' ?
                      <React.Fragment>
                        <TaskForm showTimeModal={this.showTimeModal} status={status} pageData={pageData} onChange={this.onChange}></TaskForm>
                      </React.Fragment> : <h1 className="task-perch">{langCheck('reportTimeMultiLang', '100303-000046')}{/* 国际化处理： 请新增任务*/}</h1>
                  }
                </React.Fragment>
              } //右侧区域dom
              defLeftWid='20%'
            />
          </NCModal.Body>
          {status != 'preview' && <NCModal.Footer>
            {status == 'edit' && <React.Fragment>
              <NCButton fieldid="time_confirm" colors="primary" size="large" onClick={() => this.onSaveAndAdd()}>{langCheck('reportTimeMultiLang', '100303-000021')}{/*确定新增 */}</NCButton>
              <NCButton fieldid="time_confirm" colors="primary" size="large" onClick={() => this.onSaveInfo()}>{langCheck('reportTimeMultiLang', '100303-000022')}{/*确定 */}</NCButton>
              <NCButton fieldid="time_close" colors="second" size="large" onClick={() => this.onCancel()}>{langCheck('reportTimeMultiLang', '100303-000023')}{/*确定新增 */}</NCButton> </React.Fragment>}
          </NCModal.Footer>}
        </NCModal>
        <TimeChooseModel timingSet={pageData.timeConfig} ref="timeChooseModal" onTimeModalEnter={this.onTimeModalEnter} />
      </React.Fragment>
    )
  }
}

export default TimeModal = createPage({})(TimeModal);
