import React, { Component } from 'react';
import { Rnd } from 'react-rnd';
import { createPage, base, ajax, toast, getMultiLang, createPageIcon } from 'nc-lightapp-front';
import {
	clickSearchBtn,
	initTemplate,
	tableRender,
	areaCellsFun,
	blockFun,
	sendSearchFun,
	myRender,
	afterSelection,
	relationSearch,
	turnUpIcon,
	turnDownIcon,
	shareReport,
	commomPrint,
	templatePrint,
	getFilterData,
	cancelFilter,
	closeFilterModal,
	getPagerData,
	rollingFilterClick,
	showPagerClick,
	rollingUpDown,
	getFilterInfo,
	shareMenuClick,
	filterMenuClick,
	getInitData
} from './events';
import Handsontable from '../../../public/office';
import Pagintion from '../../../public/pagination';
import Rolling from '../../../public/rolling';
const { NCDropdown, NCMenu, NCButton, NCHotKeys, NCTooltip } = base;
import FilterModal from '../../../public/filterModal';
import PagerModal from '../../../public/pagerModal';
import ShareModal from '../../../public/shareModal';
import OutputModal from '../../../public/outputModal';
import ConfirmModal from '../../../public/confirmModal';
import ShareDetailModal from '../../../public/shareDetailModal';
import TimeModal from '../../../public/timeModal';
import Utils from '../../../public/utils';
import GraphicReport from '../../graphic'
const { isFunction, addClassName, removeClassName, langCheck, getGlobalStorage, removeGlobalStorage } = Utils;
const { Item, NCDivider } = NCMenu;
require('./index.less');

let documentHidden = 'webkitHidden';

class SimpleReport extends Component {
	// 由于更改state时会重绘，所以操作了一部分DOM
	constructor(props) {
		super(props);
		const _this = this;
		this.searchId = 'light_report';
		this.isConnection = false; //联查标记
		this.isClickSearchBtn = false;
		this.sendSearchParams = {
			pageInfo: {
				pageIndex: '1',
				pageSize: '200'
			}
		}; //查询参数暂存
		this.coords = {};
		this.isRange = true; //按钮在没有数据且没有在指定范围内不可点击
		this.sortAndFilterArea = false;
		//this.toggleSearchArea = false; //显示隐藏查询区
		this.preloadMaxRow = '-1'; //是否显示分页中的真实分页信息
		this.isClickShowAllCount = false; //是否点击了分页中的显示行数
		this.key_time_offset = (-8 - new Date().getTimezoneOffset() / 60).toString(); //获取时区
		this.commonParams = {
			pk_report: '',
			reportId: '',
			queryAreaCode: '',
			oid: '',
			opents: '', //报表时间戳
			appcode:
				(props.ownReportParams && props.ownReportParams.appcode) ||
				props.getUrlParam('appcode') ||
				props.getSearchParam('c') ||
				'',
			pageCode:
				(props.ownReportParams && props.ownReportParams.pagecode) ||
				props.getUrlParam('pageCode') ||
				props.getSearchParam('p') ||
				'',
			querytype: 'tree',
			isAdvanced: false, //判断是高级查询还是普通查询
			pageInfoForDim: [], //页维度的参数
			transSaveObject: '',
			keyIsExecDrill: false // false为正常打开报表，true为联查的报表，在联查时设为true，不点查询为ture,点查询为false
		}; //相同参数存放处
		this.initParams = {
			//初始化数据参数
			dataRowNum: '', //分页中如果为0则显示条数0
			noDropdownPrint: true, //打印，打印菜单可用性
			excelPrintMaxRow: '', //excel最大导出量
			pdfPrintMaxRow: '', //pdf最大导出量
			key_CUR_USER_ID: '', //打印超出时分享给的用户
			isShowZero: true, //当返回为false时，表格区字段是0不显示
			drillConditions: [], // 联查带过来的查询条件
			printmenu: [], //打印菜单
			uSearchMenuArr: [], //联查下拉菜单
			repCondition: {}, //初始页维度数据
			pagerData: {
				// 页维度数据
				count: 0, //上下页的索引
				upDownRollingArray: null, //上下页后台发送参数
				pageInfo: null,
				flag: true, //是否显示上下页
				type: -1, //显示哪种交互方式
				modalRollingArray: null //筛选模态框数据
			},
			controlByLicense: false, //鉴权
		};
		this.pageParams = {
			//页面参数
			FreeReport_QueryTreeVo: '', // 打印需要的序列化参数
			reportType: '2', //判断是什么表
			condition: { logic: 'and', conditions: [] } // 查询参数
		};
		this.sortStyle = {
			//排序后图标显示
			headerSwitch: null,
			headStyle: '',
			sortStyles: [],
			cancelSortIndex: null
		};
		this.filterStyle = {
			//筛选后图标显示
			filterColIndexArray: [], //记录上一下筛选的单元格
			cancelFilterIndex: null //记录取消筛选的那一列
		};
		this.isReportDrillParams = {}; //保存一份联查接口参数，用于联查过来后直接点显示合计按钮的请求参数
		this.state = {
			showTimeModal: false, //定时分享模态框
			timingList: [], //定时分享左侧的列表
			graphicParams: {
				isDefaultExpand: false, //是否显示侧拉组件模态框
				pk_storyboard: '', //故事板pk
				needLoadWhenQuery: false, //查询时是否加载数据
				needRefreshWhenClickRowData: false, //点击行数据是否刷新数据
				search_params: {}, //查询区参数
				analysis_params: {}, //侧边栏分析参数
				hideTopBar: true
			}, //图形化侧拉参数
			fileName: props.getUrlParam('n') || props.getSearchParam('n') || '',
			noDropdownClick: true, //联查下拉菜单可点击性
			ShareDetailData: null, //分享明细数据
			fldAreaArray: [], //每一列的有效区域
			operationQueue: '', //查询 筛选 排序 分页 区分字段
			outputModalData: null, //打印弹出框数据
			nodekey: '', //打印参数
			pagerModalData: {
				data: null, //页维度模态框数据
				index: '' //页维度模态框索引
			},
			rollingPagerData: [], //可点击页维度展示数据
			area: [], //表格数据拥有的范围
			result: null, //总表格数据
			tdStyle: null, //格式化后的样式
			exportShow: false, //是否显示导出item
			pager: {
				allRowCount: 0,
				pageCount: 1,
				pageIndex: 1,
				pageRowCount: 0,
				pageSize: 200
			},
			settings: {
				copyPaste: {
					columnsLimit: 3000,
					rowsLimit: 3000
				},
				data: [], // 表格数据
				width: '100%',
				//height: 623,
				stretchH: 'all', // 排列方式
				colHeaders: true,
				rowHeaders: true,
				manualColumnResize: true,
				manualRowResize: true,
				viewportRowRenderingOffset: 20,
				//renderAllRows: true,
				fixedRowsTop: 0,
				fixedColumnsLeft: 0,
				currentRowClassName: 'checkedRow',
				currentColClassName: 'checkedCol',
				mergeCells: [], // 合并表格
				//minSpareCols: 1, //最后永远多一列空白列
				readOnly: true,
				rowHeights: [], //单元格高度数组
				colWidths: [], //单元格宽度数组
				cells: function (row, col, prop) {
					this.renderer = myRender.bind(_this);
				}
			}
		};
		if (props.getUrlParam('webDrillType') !== '2' && props.getSearchParam('webDrillType') !== '2') {
			initTemplate.call(this, props);
		}
	}

	componentWillMount() {
		let callback = (json) => {
			window.reportMultiLang = json;
		};
		getMultiLang({ moduleId: 100301, currentLocale: 'zh-CN', domainName: 'lappreportrt', callback });
	}

	componentDidMount() {
		let sessionFlag = getGlobalStorage('localStorage', 'LinkReport') || getGlobalStorage('sessionStorage', 'LinkReport');
		if (!sessionFlag) {
			this.getData();
		}
		let visibilityChange;
		if (typeof document.mozHidden !== 'undefined') {
			documentHidden = 'mozHidden';
			visibilityChange = 'mozvisibilitychange';
		} else if (typeof document.msHidden !== 'undefined') {
			documentHidden = 'msHidden';
			visibilityChange = 'msvisibilitychange';
		} else if (typeof document.webkitHidden !== 'undefined') {
			documentHidden = 'webkitHidden';
			visibilityChange = 'webkitvisibilitychange';
		} else if (typeof document.hidden !== 'undefined') {
			documentHidden = 'hidden';
			visibilityChange = 'visibilitychange';
		}
		document.addEventListener(visibilityChange, this.onVisibilityChanged, false);
	}

	componentWillUnmount() {
		removeGlobalStorage('localStorage', 'LinkReport')
		removeGlobalStorage('sessionStorage', 'LinkReport')
	}

	onVisibilityChanged(event) {
		var hidden = event.target[documentHidden];
		if (hidden) {
			removeGlobalStorage('localStorage', 'LinkReport')
			removeGlobalStorage('sessionStorage', 'LinkReport')
		}
	}

	getData = () => {
		if (this.props.isBusiRender) {
			//如果走业务组自定义渲染
			let doBusiSearch = this.props.isBusiRender();
			doBusiSearch.then((result) => {
				getInitData.call(this, result.data, false, true, false);
			});
			return;
		}
		if (this.props.isRenderFromDesign) {
			//如果走自助分析设计
			let doBusiSearch = this.props.isRenderFromDesign();
			doBusiSearch.then((result) => {
				getInitData.call(this, result, false, true, false);
			});
			return;
		}
		// 初始数据
		const { getUrlParam } = this.props;
		const { appcode, pageCode } = this.commonParams;
		const row = getUrlParam('row') || ''; //动态url传参
		const column = getUrlParam('column') || ''; //动态url传参
		const targetRepPk = getUrlParam('targetRepPk') || ''; //动态url传参
		const dirllcode = getUrlParam('drillCode') || ''; //动态url传参
		const reportId = getUrlParam('reportId') || ''; //动态url传参
		const opents = getUrlParam('opents') || ''; //动态url传参
		const operationQueue = getUrlParam('operationQueue') || ''; //动态url传参
		const oid = getUrlParam('oid') || ''; //动态url传参
		const pageIndex = getUrlParam('pageIndex') || ''; //动态url传参
		const pageSize = getUrlParam('pageSize') || ''; //动态url传参
		const webDrillType = getUrlParam('webDrillType') || ''; //动态url传参'
		let data, url;

		let flag = false;
		let noDropdownPrint = true;
		if (row && column) {
			//走联查
			flag = true;
			noDropdownPrint = false;
			let condition = getGlobalStorage('sessionStorage', `condition${reportId}*${dirllcode}`);
			let transSaveObject = getGlobalStorage('sessionStorage', `trans${opents}${reportId}`);
			data = {
				row,
				column,
				reportId,
				targetRepPk,
				dirllcode,
				transSaveObject,
				opents,
				appcode,
				pageCode,
				querytype: 'tree',
				pageInfo: {
					pageIndex,
					pageSize
				},
				querycondition: JSON.parse(condition),
				oid,
				operationQueue,
				webDrillType: webDrillType.toString()
			};
			this.isReportDrillParams = data;
			url = '/nccloud/report/widget/lightreport_reportdrill.do';
		} else {
			data = { appcode };
			url = '/nccloud/report/widget/lightreport_query.do';
		}
		ajax({
			url,
			data,
			success: (res) => {
				if (res.success) {
					//当时联查过来时，如不点查询直接点显示合计，则走联查的接口，如点查询后，则按正常逻辑走
					if (flag) {
						this.isClickShowAllCount = true;
						this.commonParams.keyIsExecDrill = true;
					}
					getInitData.call(this, res, flag, noDropdownPrint, row);
				}
			}
		});
	};

	handleClick(way) {
		// 表格几个使用方法
		const { pager, operationQueue } = this.state;
		const { condition } = this.pageParams;

		if (way == 'sideBox') {
			this.setState({
				graphicParams: {
					...this.state.graphicParams,
					isDefaultExpand: !this.state.graphicParams.isDefaultExpand
				}
			}, () => this.refs.hansontable.refs.hot.hotInstance.updateSettings(this.state.settings))
			return;
		}

		if (way == 'row_resize') {
			this.rowResize();
			return;
		}

		if (way == 'share') {
			if (this.props.isBusiShareFun) {
				const { pager, operationQueue, fileName } = this.state;
				const { key_CUR_USER_ID } = this.initParams;
				const { condition } = this.pageParams;
				const { allRowCount } = pager;
				let maxrow = this.preloadMaxRow != -1 && this.preloadMaxRow <= allRowCount ? '-1' : allRowCount;
				let shareObj = {
					fileName,
					maxrow,
					operationQueue,
					key_CUR_USER_ID,
					pageInfo: {
						pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
						pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
					},
					querycondition: condition,
					...this.commonParams
				};
				this.props.isBusiShareFun(shareObj)
				return
			}
			this.shareFun();
			return;
		}

		if (way == 'highBtn') {
			if (this.props.isNoSearchItem) {
				// 只显示高级查询按钮，点击是否显示高级查询及是否直接走查询
				clickSearchBtn.call(this, this.props, this.props, null, 'simple', {});
			} else {
				this.props.search.openAdvSearch(this.searchId, true);
			}
			return;
		}

		if (way == 'cancel_block') {
			this.setState({
				settings: {
					...this.state.settings,
					fixedRowsTop: 0,
					fixedColumnsLeft: 0,
					mergeCells: areaCellsFun(this.state.result)
				}
			});
			this.isBlock = false;
			return;
		}
		let coords = this.coords;
		if (coords.row == null || coords.row == undefined || coords.row == '' || coords.row < 0 || coords.col < 0) {
			toast({ content: langCheck('reportMultiLang', '100301-000009'), color: 'warning' }); /* 国际化处理： 请选择数据单元格*/
			return;
		}

		let sortType =
			way == 'asc' ? 'sort_asc' : way == 'desc' ? 'sort_desc' : way == 'cancel_sort' ? 'sort_none' : '';
		let param = {
			row: coords.row.toString(),
			column: coords.col.toString(),
			operationQueue: operationQueue,
			pageInfo: {
				pageIndex: (pager.pageIndex && pager.pageIndex.toString()) || '',
				pageSize: (pager.pageSize && pager.pageSize.toString()) || ''
			},
			querycondition: condition
		};
		let data;
		if (way == 'asc' || way == 'desc' || way == 'cancel_sort') {
			data = Object.assign({}, param, this.commonParams, { sortType });
		} else if (way == 'filter') {
			data = Object.assign({}, param, this.commonParams, { filterValues: [] });
		} else if (way == 'cancel_filter') {
			data = Object.assign({}, param, this.commonParams, { filterValues: [] }, { removeitem: 'remove' });
		}
		if (way == 'filter') {
			getFilterInfo.call(this, data);
		} else if (way == 'cancel_filter') {
			cancelFilter.call(this, data);
		} else if (way == 'asc' || way == 'desc' || way == 'cancel_sort') {
			this.sortFun(data);
		}
	}

	shareFun() {
		// 分享模态框
		this.refs.shareModal.setState({
			showShareModal: true
		});
	}

	rowResize() {
		//更改单元格宽度
		const { pk_report, opents } = this.commonParams;
		let colWidths = this.refs.hansontable.returnData().colWidths;
		for (let i = 0; i < colWidths.length; i++) {
			colWidths[i] = Math.ceil(colWidths[i]) + '';
			if (colWidths[i] == 0.01) {
				colWidths[i] = '0';
			}
		}
		let data = {
			pk_report,
			width: colWidths,
			isBusinessSet: 'false',
			opents
		};
		ajax({
			url: '/nccloud/report/widget/lightreport_savewidth.do',
			data: data,
			success: (res) => {
				if (res.success) {
					toast({
						content: langCheck('reportMultiLang', '100301-000013'),
						color: 'success'
					}); /* 国际化处理： 保存成功*/
				}
			}
		});
	}

	showConfirm = () => {
		this.refs.confirmModal.setState({
			showConfirmModal: true
		});
	};

	sureConfirmModal = () => {
		shareReport.call(this, 'print');
	};

	sortFun(data) {
		const { result } = this.state;
		if (result.reportType == 1 && this.isClickCoords.col > 0) {
			toast({ content: `${langCheck('reportMultiLang', '100301-000102')} ${this.sortToastText} ${langCheck('reportMultiLang', '100301-000103')}`, color: 'danger' });
			return;
		}
		ajax({
			url: '/nccloud/report/widget/lightreport_sort.do',
			data,
			success: (res) => {
				if (res.success) {
					let { column, sortType } = data;
					let { sortStyles, cancelSortIndex } = this.sortStyle;
					let headerSwitch = column,
						headStyle;
					switch (data.sortType) {
						case 'sort_none':
							sortStyles = sortStyles.filter(item => item.index != column)
							cancelSortIndex = column;
							addClassName(document.getElementsByClassName('sortDisabled')[0]);
							break;
						case 'sort_asc':
							let obj = {
								index: column,
								style: 'asc'
							}
							sortStyles = this.addLastSort(sortStyles, obj)
							cancelSortIndex = null;
							addClassName(document.getElementsByClassName('fitletDisabled')[0]);
							addClassName(document.getElementsByClassName('sortDisabled')[0]);
							let sortFilterBtns = document.getElementsByClassName('sortAndFilterArea');
							for (let i = 0; i < sortFilterBtns.length; i++) {
								addClassName(sortFilterBtns[i]);
							}
							break;
						case 'sort_desc':
							let obj1 = {
								index: column,
								style: 'desc'
							}
							sortStyles = this.addLastSort(sortStyles, obj1)
							cancelSortIndex = null;
							addClassName(document.getElementsByClassName('fitletDisabled')[0]);
							addClassName(document.getElementsByClassName('sortDisabled')[0]);
							let sortFilterBtns1 = document.getElementsByClassName('sortAndFilterArea');
							for (let j = 0; j < sortFilterBtns1.length; j++) {
								addClassName(sortFilterBtns1[j]);
							}
							break;
					}
					this.sortStyle = {
						sortStyles,
						cancelSortIndex
					};
					tableRender(this, res.data);
				}
			}
		});
	}

	addLastSort = (data, obj) => {
		data = data.filter(item => {
			if (item && item.index) return item.index != obj.index
		})
		data.push(obj);
		return data;
	}

	afterGetColHeader = (col, th) => {
		//排序后更改表头样式
		const { reportType } = this.pageParams;
		const { filterColIndexArray, cancelFilterIndex } = this.filterStyle;
		const { sortStyles, cancelSortIndex } = this.sortStyle;
		if (sortStyles.length > 0) {
			sortStyles.forEach(item => {
				if (item.index == col) {
					th.classList.add(item.style);
				}
			})
		}
		if (cancelSortIndex == col) {
			th.classList.remove('asc');
			th.classList.remove('desc');
		}
		if (filterColIndexArray.length > 0 && reportType != 1) {
			filterColIndexArray.forEach((item) => {
				if (item == col) {
					th.classList.add('filter');
				}
			});
		}
		if (cancelFilterIndex == col) {
			th.classList.remove('filter');
		}
	};

	pageClick(val) {
		//分页
		this.state.pager.pageIndex = val;
		getPagerData.call(this, 'pager');
	}

	getRealCount = () => {
		//显示真实数据条数
		let { condition } = this.pageParams;
		const { getUrlParam } = this.props;
		const dirllcode = getUrlParam('drillCode') || ''; //动态url传参
		const reportId = getUrlParam('reportId') || ''; //动态url传参
		const row = getUrlParam('row') || ''; //动态url传参
		this.isClickShowAllCount = false;
		condition ? condition : (condition = JSON.parse(getGlobalStorage('sessionStorage', `condition${reportId}*${dirllcode}`)));
		if (row) {
			//如果是联查直接点显示合计则走此逻辑，在点查询时会把 this.isClickShowAllCount = true
			sendSearchFun(this, this.props, this.props, condition, 'simple', {}, this.custcondition, 'showAllCount', true);
		} else {
			sendSearchFun(this, this.props, this.props, condition, 'simple', {}, this.custcondition, 'showAllCount', false);
		}
	};

	handleChange = (val) => {
		this.state.pager.pageSize = val;
	};

	afterOnCellMouseDown = (e, coords, td) => {
		if (this.state.graphicParams.needRefreshWhenClickRowData && this.isClickSearchBtn) {
			let { fldAreaArray } = this.state;
			if (fldAreaArray[coords.col]) {
				for (let i = 0; i < fldAreaArray[coords.col].length; i++) {
					if (coords.row <= fldAreaArray[coords.col][i].endRow && coords.row >= fldAreaArray[coords.col][i].startRow) {
						this.getClickRowParams(coords);
					}
				}
			}
		}
		let data_drillcode = e.target.getAttribute('data_drillcode');
		let tdSelected = this.refs.hansontable.refs.hot.hotInstance.getSelected();
		this.tdSelected = tdSelected;
		this.isClickCoords = coords;
		document.getElementById('totalSumNum').innerHTML = '';
		document.getElementById('totalCountNum').innerHTML = '';
		if (data_drillcode && data_drillcode != null) {
			setTimeout(() => {
				relationSearch.call(this, data_drillcode);
			}, 200);
		}

		if (this.props.getRowData) {
			//获取行数据
			let fieldExpCaptionMap = this.state.result.fieldExpCaptionMap;
			let areas = this.state.result.CellsModel.AreaDatas; //area [7hang, 0lie dao, 10hang, 36lie] {hang: 9, lie: 2}
			let data = this.state.result.CellsModel.DynamicModel[0].cellsArray[coords.row]
				? JSON.parse(JSON.stringify(this.state.result.CellsModel.DynamicModel[0].cellsArray[coords.row]))
				: [];
			if (areas.length > 0) {
				for (let i = 0; i < areas.length; i++) {
					if (
						areas[i].area[0] <= coords.row &&
						areas[i].area[2] >= coords.row &&
						areas[i].area[1] <= coords.col &&
						areas[i].area[3] >= coords.col
					) {
						for (let j = 0; j < areas[i].fldAreas.length; j++) {
							if (
								areas[i].fldAreas[j].fldArea[0][0] <= coords.row &&
								areas[i].fldAreas[j].fldArea[0][2] >= coords.row
							) {
								areas[i].fldAreas[j].fldDisplay = fieldExpCaptionMap[areas[i].fldAreas[j].fldName];
								data[areas[i].fldAreas[j].fldArea[0][1]].push(areas[i].fldAreas[j]);
							}
						}
					}
				}
			}
			return this.props.getRowData(data, coords);
		}
	};

	getClickRowParams = (coords) => {
		if (this.isClickCoords && this.isClickCoords.row && this.isClickCoords.row == coords.row) return true;
		//获取图形化点击单元格时的数据
		ajax({
			url: '/nccloud/report/widget/graphicbeforerefreshaction.do',
			data: {
				clickPos: {
					row: coords.row.toString(),
					column: coords.col.toString()
				},
				...this.sendSearchParams
			},
			success: res => {
				if (this.props.coverRowData) {
					let doCoverRowData = this.props.coverRowData(res.data.reportRowData);
					doCoverRowData.then((result) => {
						this.setState({
							graphicParams: {
								...this.state.graphicParams,
								pk_storyboard: res.data.pk_storyboard,
								analysis_params: res.data.analysis_params,
								reportRowData: result
							}
						})
					});
				} else {
					this.setState({
						graphicParams: {
							...this.state.graphicParams,
							pk_storyboard: res.data.pk_storyboard,
							analysis_params: res.data.analysis_params,
							reportRowData: res.data.reportRowData
						}
					})
				}

			}
		})
	}

	/**
	 * record: 传参参数
	 * index: 哪一个页维度
	 * type: 是否为下拉页维度
	 * subscript: 参照或者树结构选择的item索引，可传可不传
	 * canClick: 一个页维度为true,多个为false
	 */
	checkPaper(record, index, type, subscript, canClick) {
		if (
			subscript > -1 &&
			!canClick &&
			(this.initParams.pagerData.type == 1 || this.initParams.pagerData.type == 2)
		) {
			rollingUpDown.call(this, 'onlyUpDown', subscript);
			return;
		}
		// 选择页维度操作
		this.state.rollingPagerData[index] = record;
		this.state.rollingPagerData.length = this.initParams.repCondition.pageNum;
		if (type !== 'select') {
			this.setState({
				rollingPagerData: this.state.rollingPagerData,
				pagerModalData: {
					...this.state.pagerModalData,
					showPagerModal: false
				}
			});
		} else {
			this.setState({
				rollingPagerData: this.state.rollingPagerData
			});
		}
	}

	closePagerModal = () => {
		this.setState({
			pagerModalData: {
				...this.state.pagerModalData,
				showPagerModal: false
			}
		});
	};

	onVisibleChange = (id, flag) => {
		const { pager } = this.state;
		const { noDropdownPrint } = this.initParams;
		if (id !== 'dongjie' && id !== 'liancha') {
			let iconDOM = document.getElementById(id).getElementsByTagName('i')[0];
			if (flag) {
				turnUpIcon(iconDOM);
			} else {
				turnDownIcon(iconDOM);
			}
		} else {
			if (id == 'liancha') this.cellPerch = !this.cellPerch;
			let iconDOM = document.getElementById(id);
			if (flag) {
				turnUpIcon(iconDOM);
			} else {
				turnDownIcon(iconDOM);
			}
		}
		//下拉菜单是否可点击
		if (id == 'liancha') {
			if (this.sortAndFilterArea && !noDropdownPrint && !this.emptyDataFlag) {
				this.setState({
					noDropdownClick: false
				});
			} else {
				this.setState({
					noDropdownClick: true
				});
			}
		}
	};

	onClose = () => {
		this.setState({
			graphicParams: {
				...this.state.graphicParams,
				isDefaultExpand: false
			}
		})
	}

	onTimeModalHide = () => {
		this.setState({
			showTimeModal: false
		})
	}

	render() {
		let {
			search,
			getUrlParam,
			getSearchParam,
			showSortAndFilter,
			showHighSearchBtn,
			printOther,
			uSearchWidth,
			printWidth,
			blockWidth,
			ownReportParams
		} = this.props;
		showSortAndFilter = showSortAndFilter === undefined ? true : showSortAndFilter;
		showHighSearchBtn = showHighSearchBtn === undefined ? false : showHighSearchBtn;
		let { NCCreateSearch } = search;
		let coords = this.coords || {};
		const drillType =
			(ownReportParams && ownReportParams.showSearchArea) ||
			getUrlParam('webDrillType') ||
			getSearchParam('webDrillType') ||
			'0'; //动态url传参
		let menuItems = [];
		let {
			settings,
			pager,
			pagerModalData,
			rollingPagerData,
			outputModalData,
			ShareDetailData,
			noDropdownClick,
			fileName,
			graphicParams,
			operationQueue
		} = this.state;
		let { dataRowNum, noDropdownPrint, printmenu, uSearchMenuArr, repCondition, pagerData } = this.initParams;
		const { reportType } = this.pageParams;
		let searchStyle = {},
			printSelectClass = '';
		pagerData && pagerData.type != -1
			? (searchStyle = { borderBottom: '1px solid #ccc' })
			: (searchStyle = { borderBottom: 'none' });
		if (drillType == '2') {
			noDropdownPrint = false;
			printSelectClass = 'print-position';
		}
		uSearchMenuArr.length > 0 &&
			uSearchMenuArr.map((item) => {
				menuItems.push(
					<Item disabled={noDropdownClick ? true : false} key={item.drillCode} fieldid={item.drillName} title={item.drillName}>
						{item.drillName}
					</Item>
				);
			});
		const blockMenu = (
			<NCMenu onClick={blockFun.bind(this)}>
				<Item key="lockRow" fieldid="lockRow">{langCheck('reportMultiLang', '100301-000022')}</Item>
				{/* 国际化处理： 冻结行*/}
				<Item key="lockColumn" fieldid="lockColumn">{langCheck('reportMultiLang', '100301-000023')}</Item>
				{/* 国际化处理： 冻结列*/}
				<Item key="lockRowColumn" fieldid="lockRowColumn">{langCheck('reportMultiLang', '100301-000024')}</Item>
				{/* 国际化处理： 冻结行列*/}
			</NCMenu>
		);
		const uSearchMenu = <NCMenu onClick={relationSearch.bind(this)}>{menuItems}</NCMenu>;
		const outputMenu = (
			<NCMenu onClick={commomPrint.bind(this)}>
				<Item disabled={noDropdownPrint ? true : false} key="xlsx" fieldid="xlsx">
					{langCheck('reportMultiLang', '100301-000025')}
					{/* 国际化处理： 输出 xlsx 文件*/}
				</Item>
				<Item disabled={noDropdownPrint ? true : false} key="csv" fieldid="csv">
					{langCheck('reportMultiLang', '100301-000026')}
					{/* 国际化处理： 输出 csv 文件*/}
				</Item>
				<Item disabled={noDropdownPrint ? true : false} key="pdf" fieldid="pdf">
					{langCheck('reportMultiLang', '100301-000027')}
					{/* 国际化处理： 输出 pdf 文件*/}
				</Item>
				<NCDivider />
				{printmenu.length > 0 &&
					printmenu.map((item) => (
						<Item disabled={noDropdownPrint ? true : false} key={item.nodekey} title={item.name} fieldid={item.name}>
							{item.name}
						</Item>
					))}
				{printOther &&
					printOther.length > 0 &&
					printOther.map((item) => (
						<Item
							disabled={noDropdownPrint || item.disabled ? true : false}
							key={item.key}
							fieldid={item.display}
							printExpend={true}
							title={item.display}
						>
							{item.display}
						</Item>
					))}
			</NCMenu>
		);
		const shareMenu = (
			<NCMenu onClick={shareMenuClick.bind(this)}>
				<Item key="time" disabled={noDropdownPrint ? true : false} fieldid="shareTime">{langCheck('reportMultiLang', '100301-000100')}</Item>
				{/* 国际化处理： 定时分享*/}
				<Item key="xlsx" fieldid="shareStatus">{langCheck('reportMultiLang', '100301-000028')}</Item>
				{/* 国际化处理： 分享状态*/}
			</NCMenu>
		);
		const filterMenu = (
			<NCMenu onClick={filterMenuClick.bind(this)}>
				<Item disabled={this.commonParams.keyIsExecDrill || this.isClickSearchBtn ? false : true} key="detail" fieldid="detail">
					{langCheck('reportMultiLang', '100301-000096')}
				</Item>
				{/* 国际化处理： 仅明细*/}
				<Item disabled={this.commonParams.keyIsExecDrill || this.isClickSearchBtn ? false : true} key="all" fieldid="all">
					{langCheck('reportMultiLang', '100301-000097')}
				</Item>
				{/* 国际化处理： 仅汇总*/}
			</NCMenu>
		);
		let startSearchText = langCheck('reportMultiLang', '100301-000037');
		let searchBtnFlag = false;
		if (uSearchMenuArr.length == 1) {
			let drillName = uSearchMenuArr[0].drillName
			if (drillName && drillName.indexOf(startSearchText) > -1) {
				let index = drillName.indexOf(startSearchText);
				if (index == 0) searchBtnFlag = true;
			}
		}
		return (
			<div className="simple-report" id="simpleReport">
				<div className="search-item nc-theme-common-header-bgc nc-theme-area-split-bc" style={drillType == '2' || showHighSearchBtn ? { borderBottom: 'none' } : { borderBottom: '1px solid #d0d0d0' }} fieldid="header-area">
					<div style={{ paddingLeft: '20px', display: 'flex', alignItems: 'center' }}>
						<div>{(createPageIcon && createPageIcon()) || null}</div>
						<h2 fieldid={`${ownReportParams && ownReportParams.reportName || fileName}_title`} className="nc-theme-title-font-c" style={{ marginLeft: '8px', fontSize: '16px', fontWeight: 'bold' }}>
							{(ownReportParams && ownReportParams.reportName) || fileName}
						</h2>
					</div>
					<div style={{ paddingRight: '8px' }}>
						{showHighSearchBtn &&
							drillType !== '2' && (
								<div className="btn-box">
									<NCButton
										fieldid="search"
										shape="border"
										colors="primary"
										className="btn high-btn"
										onClick={this.handleClick.bind(this, 'highBtn')}
									>
										{langCheck('reportMultiLang', '100301-000029')}
										{/* 国际化处理： 查询*/}
									</NCButton>
								</div>
							)}
						{drillType !== '2' && showSortAndFilter ? (
							<div className="btn-box">
								<NCButton
									fieldid="asc"
									className="btn left-radius sortAndFilterArea"
									shape="border"
									onClick={this.handleClick.bind(this, 'asc')}
								>
									{langCheck('reportMultiLang', '100301-000030')}
									{/* 国际化处理： 升序*/}
								</NCButton>
								<NCButton
									fieldid="desc"
									className="btn not-radius sortAndFilterArea"
									shape="border"
									onClick={this.handleClick.bind(this, 'desc')}
								>
									{langCheck('reportMultiLang', '100301-000031')}
									{/* 国际化处理： 降序*/}
								</NCButton>
								<NCButton
									fieldid="cancelSort"
									className="btn right-radius sortDisabled"
									shape="border"
									onClick={this.handleClick.bind(this, 'cancel_sort')}
								>
									{langCheck('reportMultiLang', '100301-000032')}
									{/* 国际化处理： 取消排序*/}
								</NCButton>
							</div>
						) : null}
						{drillType !== '2' && showSortAndFilter ? (
							<div className="btn-box">
								<NCButton
									fieldid="filter"
									className="btn left-radius sortAndFilterArea"
									shape="border"
									onClick={this.handleClick.bind(this, 'filter')}
								>
									{langCheck('reportMultiLang', '100301-000033')}
									{/* 国际化处理： 筛选*/}
								</NCButton>
								<NCButton
									fieldid="cancelFilter"
									className="btn right-radius fitletDisabled"
									shape="border"
									onClick={this.handleClick.bind(this, 'cancel_filter')}
								>
									{langCheck('reportMultiLang', '100301-000034')}
									{/* 国际化处理： 取消筛选*/}
								</NCButton>
							</div>
						) : null}
						{drillType !== '2' ? (
							<div className="btn-box">
								<NCDropdown
									fieldid={'freeze'}
									trigger={['click']}
									overlay={blockMenu}
									animation="slide-up"
									overlayStyle={{ maxWidth: '164px' }}
									overlayClassName="hanson-export"
									onVisibleChange={this.onVisibleChange.bind(this, 'dongjie')}
								>
									<NCButton
										fieldid="freeze"
										id="dongjie"
										style={{ paddingRight: '18px' }}
										className="btn left-radius icon iconfont icon-hangcaozuoxiala1"
										shape="border"
										disabled={this.isBlock}
									>
										<span className="block-item">
											{langCheck('reportMultiLang', '100301-000035')}
										</span>
										{/* 国际化处理： 冻结*/}
									</NCButton>
								</NCDropdown>
								<NCButton
									fieldid="cancelFreeze"
									className="btn right-radius"
									shape="border"
									onClick={this.handleClick.bind(this, 'cancel_block')}
									disabled={!this.isBlock}
								>
									{langCheck('reportMultiLang', '100301-000036')}
									{/* 国际化处理： 取消冻结*/}
								</NCButton>
							</div>
						) : null}

						{uSearchMenuArr.length > 0 && (
							<div className="btn-box">
								{uSearchMenuArr.length > 1 ?
									<NCDropdown
										fieldid="drill"
										trigger={['click']}
										overlay={uSearchMenu}
										animation="slide-up"
										overlayStyle={{ maxWidth: '164px' }}
										overlayClassName="hanson-export"
										onVisibleChange={this.onVisibleChange.bind(this, 'liancha')}
									>
										<NCButton
											fieldid="drill"
											id="liancha"
											style={{ paddingRight: '18px' }}
											className="sortAndFilterArea btn icon iconfont icon-hangcaozuoxiala1"
											shape="border"
										>
											<span className="block-item">
												{langCheck('reportMultiLang', '100301-000037')}
											</span>
											{/* 国际化处理： 联查*/}
										</NCButton>
									</NCDropdown> :
									<NCButton
										fieldid="drill"
										className="sortAndFilterArea btn"
										shape="border"
										onClick={relationSearch.bind(this, uSearchMenuArr[0].drillCode)}
									>
										<span className="block-item">
											{searchBtnFlag ? uSearchMenuArr[0].drillName : `${langCheck('reportMultiLang', '100301-000037')}${uSearchMenuArr[0].drillName || ' '}`}
										</span>
										{/* 国际化处理： 联查*/}
									</NCButton>
								}

							</div>
						)}

						<div className="btn-box" id="dayin">
							<NCHotKeys
								style={{ padding: 0, float: 'left' }}
								keyMap={{
									// key为内置常量  例如 NC_ADD => ctrl+/
									addActionSign: NCHotKeys.USUAL_KEYS.NC_BTN_PRINT
								}}
								handlers={{
									addActionSign: () => {
										if (noDropdownPrint) return;
										commomPrint.call(this, 'pdf', 'noWay');
									}
								}}
								// 触发区域  默认为 document.body
								focused={true}
							>
								<NCTooltip overlay={`${langCheck('reportMultiLang', '100301-000038')} (Ctrl+P)`} inverse placement="bottom">
									<NCButton
										fieldid="print"
										shape="border"
										className="btn left-radius"
										style={{ minWidth: 52, marginLeft: 0 }}
										onClick={commomPrint.bind(this, 'pdf', 'noWay')}
										disabled={noDropdownPrint}
									>
										{langCheck('reportMultiLang', '100301-000038')}
										{/* 国际化处理： 打印*/}
									</NCButton>
								</NCTooltip>
							</NCHotKeys>
							<NCDropdown
								fieldid="print"
								trigger={['click']}
								overlay={outputMenu}
								animation="slide-up"
								overlayStyle={{ maxWidth: '164px' }}
								overlayClassName={`hanson-export position-left ${printSelectClass}`}
								getPopupContainer={() => document.getElementById('dayin')}
								onVisibleChange={this.onVisibleChange.bind(this, 'dayin')}
							>
								<NCButton className={'output-container'} fieldid="print" is_arrow={true}>
									<i className={'icon iconfont icon-hangcaozuoxiala1'} />
								</NCButton>
							</NCDropdown>
						</div>

						{drillType !== '2' && this.props.hideShareBtn !== true ? (
							<div className="btn-box" id="fenxiang">
								<NCButton
									fieldid="share"
									className="btn left-radius"
									shape="border"
									onClick={this.handleClick.bind(this, 'share')}
									disabled={noDropdownPrint}
								>
									{langCheck('reportMultiLang', '100301-000039')}
									{/* 国际化处理： 分享*/}
								</NCButton>
								<NCDropdown
									fieldid="share"
									trigger={['click']}
									overlay={shareMenu}
									animation="slide-up"
									overlayStyle={{ maxWidth: '164px' }}
									overlayClassName="hanson-export position-left"
									getPopupContainer={() => document.getElementById('fenxiang')}
									onVisibleChange={this.onVisibleChange.bind(this, 'fenxiang')}
								>
									<NCButton className="output-container" fieldid="share" is_arrow={true}>
										<i className={'icon iconfont icon-hangcaozuoxiala1'} />
									</NCButton>
								</NCDropdown>
							</div>
						) : null}
						{this.props.isShowDetailSelect && (
							<div className="btn-box">
								<NCDropdown
									fieldid="detailList"
									trigger={['click']}
									overlay={filterMenu}
									animation="slide-up"
									overlayStyle={{ maxWidth: '164px' }}
									overlayClassName={`hanson-export`}
								// getPopupContainer={() => document.getElementById('liancha')}
								// onVisibleChange={this.onVisibleChange.bind(this, 'liancha')}
								>
									<NCButton shape="border" className="btn" fieldid="detail">
										{langCheck('reportMultiLang', '100301-000095')}
										{/* 国际化处理： 明细/汇总*/}
									</NCButton>
								</NCDropdown>
							</div>
						)}

						{drillType !== '2' ? (
							<div className="btn-box">
								<NCButton
									fieldid="fixWidth"
									shape="border"
									className="btn"
									onClick={this.handleClick.bind(this, 'row_resize')}
								>
									{langCheck('reportMultiLang', '100301-000040')}
									{/* 国际化处理： 固定列宽*/}
								</NCButton>
							</div>
						) : null}

						{graphicParams.pk_storyboard ? (
							<div className="btn-box">
								<NCButton
									fieldid="graphic"
									shape="border"
									className={`btn ${window.top.nccColor === 'black' ? 'black-graphic' : graphicParams.isDefaultExpand ? "active-graphic" : "hide-graphic"}`}
									onClick={this.handleClick.bind(this, 'sideBox')}
								>
									{graphicParams.isDefaultExpand ? langCheck('reportMultiLang', '100301-000099') : langCheck('reportMultiLang', '100301-000098')}
									{/* 国际化处理： 图表分析*/}
								</NCButton>
							</div>
						) : null}

						<div className="btn-box">
							{(isFunction(this.props.CreateNewSearchArea) &&
								this.props.CreateNewSearchArea(this.isRange, settings.data, coords)) ||
								null}
						</div>
					</div>
				</div>
				<div
					className={drillType == '2' || showHighSearchBtn ? 'hide' : 'show nc-theme-gray-area-bgc'}
					id="reportSearchArea"
					style={searchStyle}
				>
					{drillType !== '2' ? (
						NCCreateSearch(
							this.searchId, // 模块id
							{
								clickSearchBtn: clickSearchBtn.bind(this, this.props),
								showAdvBtn: this.props.showAdvBtn !== false ? true : false,
								onAfterEvent:
									this.props.onAfterEvent &&
									this.props.onAfterEvent.bind(this, this.props, this.searchId),
								onBeforeEvent: this.props.onBeforeEvent,
								showAdvSearchPlanBtn: this.props.showAdvSearchPlanBtn || true,
								replaceAdvBtnEve: this.props.replaceAdvBtnEve,
								replaceAdvBody: this.props.replaceAdvBody,
								addAdvTabs: this.props.addAdvTabs,
								addAdvBody: this.props.addAdvBody,
								oid: this.oid,
								isSynInitAdvSearch: this.props.isSynInitAdvSearchFlag || false,
								replaceRightBody: this.props.replaceRightBody,
								saveSearchPlan: this.props.saveSearchPlan,
								setRequiredByField: this.props.setRequiredByField,
								onOperateTypeChange: this.props.onOperateTypeChange &&
									this.props.onOperateTypeChange.bind(this, this.props, this.searchId),
								clickPlanEve: this.props.clickPlanEve && this.props.clickPlanEve.bind(this, this.props),
								advSearchClearEve:
									this.props.advSearchClearEve &&
									this.props.advSearchClearEve.bind(this, this.props, this.searchId),
								clickAdvBtnEve:
									this.props.clickAdvBtnEve &&
									this.props.clickAdvBtnEve.bind(this, this.props, this.searchId),
								renderCompleteEvent:
									this.props.renderCompleteEvent &&
									this.props.renderCompleteEvent.bind(this, this.props, this.searchId),
								statusChangeEvent:
									this.props.statusChangeEvent &&
									this.props.statusChangeEvent.bind(this, this.props, this.searchId),
								setInitValueEvent:
									this.props.setInitValueEvent &&
									this.props.setInitValueEvent.bind(this, this.props, this.searchId)
							}
						)
					) : null}
				</div>
				{pagerData && <div style={{ display: 'flex', borderTop: '1px solid #d0d0d0' }}>
					<Rolling
						ref="rolling"
						rollingPagerData={rollingPagerData}
						pagerData={pagerData}
						allData={repCondition && repCondition.dataArr}
						showPagerClick={showPagerClick.bind(this)}
						rollingUpDown={rollingUpDown.bind(this)}
						rollingFilterClick={rollingFilterClick.bind(this)}
						checkPaper={this.checkPaper.bind(this)}
					/>
				</div>}
				<div className="hot-table" id="reportTable" fieldid="report-area">
					<div className="report-graphic-box">
						<div style={{ minWidth: 1, flex: 1 }} fieldid="report-area">
							<Handsontable
								ref="hansontable"
								afterSelection={afterSelection.bind(this)}
								settings={settings}
								afterGetColHeader={this.afterGetColHeader}
								afterOnCellMouseDown={this.afterOnCellMouseDown}
							/>
						</div>
						{graphicParams.isDefaultExpand && <div className="graphic-box" style={{ width: this.state.graphicWidth || 800 }}>
							<Rnd
								disableDragging={true}
								position={{ x: 0, y: 0 }}
								size={{ width: this.state.graphicWidth || 800, height: '100%' }}
								onResizeStop={(e, direction, ref, delta, position) => {
									this.setState({
										graphicWidth: ref.style.width,
										...position
									}, () => this.refs.hansontable.refs.hot.hotInstance.updateSettings(this.state.settings));
								}}
							>
								<GraphicReport params={graphicParams} />
							</Rnd>
						</div>}
					</div>

					{reportType != '2' && (
						<Pagintion
							pager={pager}
							dataRowNum={dataRowNum}
							preloadMaxRow={this.preloadMaxRow}
							isClickShowAllCount={this.isClickShowAllCount}
							isConnection={this.isConnection}
							getRealCount={this.getRealCount}
							pageClick={this.pageClick.bind(this)}
							handleChange={this.handleChange}
							pageSizeChange={getPagerData.bind(this, 'ensure')}
						/>
					)}
				</div>
				<FilterModal
					ref="filterModal"
					handleClose={closeFilterModal.bind(this)}
					handleSubmmit={getFilterData.bind(this)}
				/>
				<PagerModal
					ref="pagerModal"
					pagerModalData={pagerModalData}
					checkPaper={this.checkPaper.bind(this)}
					closePagerModal={this.closePagerModal}
				/>
				<ShareModal ref="shareModal" shareReport={shareReport.bind(this)} data={this.props} />
				{ShareDetailData !== null && <ShareDetailModal ref="shareDetailModal" data={ShareDetailData} />}
				<OutputModal
					ref="outputModal"
					outputReport={templatePrint.bind(this)}
					outputModalData={outputModalData}
				/>
				<ConfirmModal ref="confirmModal" sureConfirmModal={this.sureConfirmModal} />
				<TimeModal
					show={this.state.showTimeModal}
					parentProps={this.props}
					commonParams={this.commonParams}
					operationQueue={operationQueue}
					maxrow={this.state.maxrow}
					querycondition={this.pageParams.condition}
					timingList={this.state.timingList}
					onTimeModalHide={this.onTimeModalHide} />
			</div>
		);
	}
}

SimpleReport = createPage({})(SimpleReport);
export default SimpleReport;
