const mountArray = ['$', '￥', '€'];
function isNumber(value) {
	var patrn = /^(-)?\d+(\.\d+)?$/;
	if (patrn.exec(value) == null || value == '') {
		return false;
	} else {
		return true;
	}
}

// 获取url参数
function GetQueryString(name) {
	let href = window.parent.location.href;
	href = href.split('?')[1];
	if (!href) {
		return;
	}
	href = href.split('&');
	for (let i = 0; i < href.length; i++) {
		let hrefv = href[i].split('=');
		if (hrefv[0].indexOf(name) >= 0) {
			return hrefv[1];
		}
	}
}

/* 检测类型是否为数组 */
function isFunction(param) {
	return Object.prototype.toString.call(param).slice(8, -1) === 'Function';
}


const isArrayFn = value => {
	if (typeof Array.isArray === "function") {
		return Array.isArray(value);
	} else {
		return Object.prototype.toString.call(value) === "[object Array]";
	}
}

const splitURI = (url) => {
	if (!url) return false;
	if (url.indexOf('nccloud/resources') >= 0) {
		url = url.split('nccloud/resources')[1];
		if (url.indexOf('#') >= 0) {
			url = url.split('#')[0];
		} else if (url.indexOf('?') >= 0) {
			url = url.split('?')[0];
		} else {
			url = url.split('?')[0];
		}
	} else {
		url = url.split('?')[0];
	}
	return url;
};

const deWeight = (array) => {
	// 去重
	return [...new Set(array)];
};

const getThousandNum = (num) => {
	// 数字转千分位
	return (num || 0).toString().replace(/\d+/, function (n) {
		var len = n.length;
		if (len % 3 === 0) {
			return n.replace(/(\d{3})/g, ',$1').slice(1);
		} else {
			return n.slice(0, len % 3) + n.slice(len % 3).replace(/(\d{3})/g, ',$1');
		}
	});
};

const delcommafy = (num) => {
	// 千分位转数字
	num = num.replace(/,/gi, '');
	return num;
};

const getPercentNum = (num) => {
	// 百分数转数字
	if (num.indexOf('%') >= 0) {
		num = num.split('%')[0];
	}
	return num;
};

const getFixedNum = (data) => {
	// 获取小数最大位数
	let delPercentArray = [];
	data.forEach((item) => {
		if (item.indexOf('%') >= 0 && isNumber(item.split('%')[0])) {
			if (item.indexOf('.') >= 0) {
				delPercentArray.push(item.split('%')[0].split('.')[1].length)
			} else {
				item = getPercentNum(item);
			}
		}
		if (item.indexOf('.') >= 0 && isNumber(item)) delPercentArray.push(item.split('.')[1].length);
	});
	if (delPercentArray.length < 1) return 0;
	return Math.max.apply(null, delPercentArray); //找到数组内最大值
};

const outPutTotalNum = (sumArr) => {
	let fixedNumArray = [];
	// 输出合计计算
	if (sumArr[0].length == 1 && sumArr.length == 1) return '';
	let sumNum = 0;
	if (sumArr[0] !== null && sumArr[0][0] !== null && sumArr[0][0][0].indexOf(',') >= 0) {
		//如果范围内第一个单元格为千分位显示
		sumArr.forEach((items) => {
			items &&
				items[0] &&
				items.forEach((ele) => {
					if (ele && ele[0]) {
						fixedNumArray.push(ele[0]);
						for (let i = 0; i < mountArray.length; i++) {
							if (ele[0].indexOf(mountArray[i]) >= 0) {
								let moneyNum = ele[0].split(mountArray[i])[1];
								sumNum += Number(moneyNum);
								break;
							}
						}
						if (Number(delcommafy(ele[0]))) {
							sumNum += Number(delcommafy(ele[0]));
						} else if (ele[0].indexOf('%') >= 0) {
							sumNum += Number(ele[0].split('%')[0] / 100);
						} else if (isNumber(ele[0])) {
							sumNum += Number(ele[0]);
						}
					}
				});
		});
		return getThousandNum(sumNum.toFixed(getFixedNum(fixedNumArray)));
	} else if (sumArr[0] !== null && sumArr[0][0] !== null && sumArr[0][0][0].indexOf('%') >= 0) {
		//如果范围内第一个单元格为百分数显示
		sumArr.forEach((items) => {
			items &&
				items[0] &&
				items.forEach((ele) => {
					if (ele && ele[0]) {
						fixedNumArray.push(ele[0]);
						for (let i = 0; i < mountArray.length; i++) {
							if (ele[0].indexOf(mountArray[i]) >= 0) {
								let moneyNum = ele[0].split(mountArray[i])[1];
								sumNum += Number(moneyNum);
								break;
							}
						}
						if (Number(delcommafy(ele[0]))) {
							sumNum += Number(delcommafy(ele[0]));
						} else if (ele[0].indexOf('%') >= 0) {
							sumNum += Number(ele[0].split('%')[0] / 100);
						} else if (isNumber(ele[0])) {
							sumNum += Number(ele[0]);
						}
					}
				});
		});
		return (sumNum * 100).toFixed(getFixedNum(fixedNumArray)) + '%';
	} else {
		//正常数字显示
		sumArr.forEach((items) => {
			items &&
				items[0] &&
				items.forEach((ele) => {
					if (ele && ele[0]) {
						fixedNumArray.push(ele[0]);
						for (let i = 0; i < mountArray.length; i++) {
							if (ele[0].indexOf(mountArray[i]) >= 0) {
								let moneyNum = ele[0].split(mountArray[i])[1];
								sumNum += Number(moneyNum);
								break;
							}
						}
						if (Number(delcommafy(ele[0]))) {
							sumNum += Number(delcommafy(ele[0]));
						} else if (ele[0].indexOf('%') >= 0) {
							sumNum += Number(ele[0].split('%')[0] / 100);
						} else if (isNumber(ele[0])) {
							sumNum += Number(ele[0]);
						}
					}
				});
		});
		return sumNum.toFixed(getFixedNum(fixedNumArray));
	}
};

const arraySum = (arr) => {
	// 数组求和
	return arr.reduce(function (prev, curr, idx, arr) {
		return prev + curr;
	});
};

const isIE = () => {
	if (!!window.ActiveXObject || 'ActiveXObject' in window) {
		return true;
	} else {
		return false;
	}
};

const addClassName = (ele) => {
	ele.classList.add('buttons-add-disabled');
	ele.disabled = true;
};

const removeClassName = (ele) => {
	ele.classList.remove('buttons-add-disabled');
	ele.disabled = false;
};

const disposeSearch = (data) => {
	// 处理查询区数据
	let result = data;
	let val = '';
	let obj = {};
	if (result == false) {
		return false;
	}
	if (result == true) return true;
	if (result === null) {
		obj = { condition: { logic: 'and', conditions: [] } };
		return obj;
	}
	if (result.conditions === null || result.conditions.length < 1 || !result.conditions) {
		obj = { condition: { logic: 'and', conditions: [] } };
		return obj;
	}
	result.conditions.forEach((item) => {
		if (item.field == 'dyngroup_report' || item.notSearch) {
			val += item.field + ',';
		}
		if (item.conditions) {
			val += loop(item.conditions, val);
		}
	});

	val = deWeight(val.split(',')).join(',');
	result.conditions.push({ field: 'not_search', value: { firstvalue: val, secondvalue: null } });

	obj = { condition: result };
	return obj;
};

function loop(data, str) {
	data.forEach((item) => {
		if (item.field == 'dyngroup_report' || item.notSearch) {
			str += item.field + ',';
		}
		if (item.conditions) {
			str += loop(item.conditions, str);
		}
	});
	return str;
}

const buildTree = (data, id, pid) => {
	// 平铺转树
	var treeList = data.slice(0);
	var afun = function (ys, json) {
		var len = json.length;
		while (len > 0) {
			len--;
			let oo = json[len];
			if (ys[id] == oo[pid]) {
				ys.children = ys.children || [];
				ys.children.push(oo);
				json.splice(len, 1);
			}
		}
	};
	data.forEach(function (value) {
		afun(value, treeList);
	});
	return treeList;
};

const treeToPath = (tree, paths, currentPath) => {
	//树转平铺数组
	var currentPath = currentPath || [];
	var paths = paths || [];

	for (let i = 0; i < tree.length; i++) {
		if (i !== 0) {
			currentPath.pop();
		}

		currentPath.push({ disValue: tree[i].disValue, relValue: tree[i].relValue, id: tree[i].id });

		if (tree[i].children && tree[i].children.length) {
			treeToPath(tree[i].children, paths, currentPath);
		} else {
			paths.push(currentPath.slice(0));
		}
	}

	currentPath.pop();

	return paths;
};

const getSiblings = (elm) => {
	//获取所有兄弟元素
	var a = [];
	var p = elm.parentNode.children;
	for (var i = 0, pl = p.length; i < pl; i++) {
		if (p[i] !== elm) a.push(p[i]);
	}
	return a;
};

const langCheck = (way, code) => {
	return window[way] ? (window[way][code] ? window[way][code] : '') : '';
};

const showSearchToast = (data, that) => {
	setTimeout(() => {
		if (data.hintForRowLimit) {
			toast({
				content: `${langCheck('reportMultiLang', '100301-000002')},${langCheck(
					'reportMultiLang',
					'100301-000003'
				)}${data.hintForRowLimit}${langCheck('reportMultiLang', '100301-000004')}`,
				color: 'warning'
			}); /* 国际化处理： 查询数据超出范围,本次最多显示,条*/
		} else {
			let innerHtml = '';
			if (data.pager && data.pager.allRowCount) {
				if (
					that.isClickShowAllCount &&
					data.preloadMaxRow != -1 &&
					data.preloadMaxRow <= data.pager.allRowCount
				) {
					innerHtml = `${langCheck('reportMultiLang', '100301-000005')}，${langCheck(
						'reportMultiLang',
						'100301-000006'
					)} ${data.preloadMaxRow}+ ${langCheck('reportMultiLang', '100301-000004')}`; /* 国际化处理： 查询成功,共,条*/
				} else {
					innerHtml = `${langCheck('reportMultiLang', '100301-000005')}，${langCheck(
						'reportMultiLang',
						'100301-000006'
					)} ${data.pager.allRowCount} ${langCheck('reportMultiLang', '100301-000004')}`; /* 国际化处理： 查询成功,共,条*/
				}
			} else {
				innerHtml = '';
			}
			if (data.reportType != '2') {
				if (data.dataRowNum == 0 || (data.pager && data.pager.allRowCount == 0)) {
					that.emptyDataFlag = true; //没有数据的标记
					innerHtml = langCheck('reportMultiLang', '100301-000092'); /* 国际化处理： 未查询出符合条件的数据*/
					toast({ content: innerHtml, color: 'warning' });
				} else {
					that.emptyDataFlag = false; //没有数据的标记
					toast({ content: innerHtml, color: 'success' });
				}
			}
		}
	}, 20);
};

const changeConditionWhenSearch = (
	//业务干预非查询条件 => userdefObj
	queryConditionList, // 原本的查询条件 从expandSearchVal函数得来
	specialCondition // 自定义需要扩展的查询条件集合 可以是String（一个）  也可以是Array（多个）
) => {
	let conditions = queryConditionList.conditions.slice(),
		//  LinkReportUserDefObj结构是需要传给后台一个固定的对象格式
		LinkReportUserDefObj = {
			keyReportParamWeb: {}
		},
		specialConditionIsArray = Array.isArray(specialCondition); // 判断specialCondition是否是对象类型

	// 初始化LinkReportUserDefObj，将specialCondition中的键遍历放在LinkReportUserDefObj的keyReportParamWeb中
	if (specialConditionIsArray) {
		specialCondition.map((item) => {
			LinkReportUserDefObj.keyReportParamWeb[item] = '';
		});
	} else {
		LinkReportUserDefObj[specialCondition] = '';
	}

	// 从conditions中取keyReportParamWeb对应的值后，需要将自定义的查询条件从原来的查询条件中过滤掉
	conditions = conditions.filter((item) => {
		let bool = specialConditionIsArray ? specialCondition.includes(item.field) : item.field === specialCondition;
		if (bool) {
			LinkReportUserDefObj.keyReportParamWeb = {
				...LinkReportUserDefObj.keyReportParamWeb,
				[item.field]: item.value.firstvalue
			};
		}
		return !bool;
	});
	// 将LinkReportUserDefObj存于sessionStorage中， LinkReportUserDefObj发送给后台后sessionStorage就将其销毁
	sessionStorage.setItem('LinkReportUserDefObj', JSON.stringify(LinkReportUserDefObj));

	// 将处理过的queryConditionList return出去
	return {
		conditions,
		logic: queryConditionList.logic
	};
};

