export default function myRender(nstance, td, row, col, prop, value, cellProperties) {
	let { tdStyle, settings } = this.state;
	let { isShowZero } = this.initParams;
	const { colWidths } = settings;
	var textvalue = value;

	let keys = Object.keys(tdStyle);
	keys.forEach((item) => {
		if (value && value[0] && item == value[0][1]) {
			if (tdStyle[item].font) {
				td.style.backgroundColor = `${tdStyle[item].font.bgColor}`;
				td.style.color = `${tdStyle[item].font.foreColor}`;
				td.style.fontSize = `${tdStyle[item].font.fontSize}` + 'px';
				if (tdStyle[item].font.fontStyle) {
					if (tdStyle[item].font.fontStyle == '1') {
						td.style.fontWeight = 900;
					} else if (tdStyle[item].font.fontStyle == '2') {
						td.style.fontStyle = 'italic';
					} else if (tdStyle[item].font.fontStyle == '4') {
						td.style.textDecoration = 'underline';
					}
				}
				if (tdStyle[item].line && tdStyle[item].line.topLine) {
					td.style.borderTopColor = `${tdStyle[item].line.topLine.topColor}` || '#CCC';
					td.style.borderBottomColor = `${tdStyle[item].line.bottomLine.bottomColor}` || '#CCC';
					td.style.borderLeftColor = `${tdStyle[item].line.leftLine.leftColor}` || '#CCC';
					td.style.borderRightColor = `${tdStyle[item].line.rightLine.rightColor}` || '#CCC';
					td.style.borderTopWidth = `${tdStyle[item].line.topLine.topWidth}`;
					td.style.borderBottomWidth = `${tdStyle[item].line.bottomLine.bottomWidth}`;
					td.style.borderLeftWidth = `${tdStyle[item].line.leftLine.leftWidth}`;
					td.style.borderRightWidth = `${tdStyle[item].line.rightLine.rightWidth}`;
					td.style.boxSizing = 'border-box';
				}
				if (tdStyle[item].align.hAlign == 2) {
					td.style.textAlign = 'left';
				} else if (tdStyle[item].align.hAlign == 0) {
					td.style.textAlign = 'center';
				} else if (tdStyle[item].align.hAlign == 4) {
					td.style.textAlign = 'right';
				} else {
					td.style.textAlign = 'left';
				}
				if (tdStyle[item].align.vAlign == 1) {
					td.style.verticalAlign = 'top';
				} else if (tdStyle[item].align.vAlign == 3) {
					td.style.verticalAlign = 'bottom';
				} else if (tdStyle[item].align.vAlign == 0) {
					td.style.verticalAlign = 'middle';
				} else {
					td.style.verticalAlign = 'middle';
				}
				if (tdStyle[item].align.fontFold == 1) {
					td.style.whiteSpace = 'normal';
					td.style.wordBreak = 'break-all';
					td.style.wordWrap = 'break-word';
				}
			}
		}
	});
	if (!value || typeof value === undefined || value == 0) {
		textvalue = '';
	} else {
		if (isShowZero == false && !isNaN(value[0][0]) && Number(value[0][0]) == 0) {
			textvalue = '';
		} else {
			textvalue = value[0][0];
		}
		if (value[0][3].cond.length > 0 && value[0][3].cond[0].length > 0) {
			let tdValue = value[0][3];
			if (tdValue.cond[0][0] == 'I') {
				if (tdValue.cond[0][2] != '') {
					const image = require(`../../../../../static/images/${tdValue.cond[0][2]}`);
					td.style.backgroundImage = `url(${image})`;
					td.style.backgroundPosition = '5px center';
					td.style.backgroundRepeat = 'no-repeat';
				}
				if (tdValue.cond[0][3] !== false) {
					textvalue = '';
					td.style.backgroundPosition = 'center center';
				}
			} else if (tdValue.cond[0][0] == 'N') {
				td.style.backgroundColor = `${tdStyle[tdValue.cond[0][1]].font.bgColor}`;
				td.style.color = `${tdStyle[tdValue.cond[0][1]].font.foreColor}`;
				td.style.fontSize = `${tdStyle[tdValue.cond[0][1]].font.fontSize}` + 'px';
			} else if (tdValue.cond[0][0] == 'D') {
				let percent = (tdValue.cond[0][2] * 100).toFixed(0) + '%';
				td.style.background = `linear-gradient(to right, ${tdValue
					.cond[0][3]} 0%, white ${percent}, white 100%)`;
				if (tdValue.cond[0][4] !== false) {
					textvalue = '';
				}
			}
		}
		td.title = textvalue.replace(/&nbsp;/ig, ' ');
		if (value[0][3].nccloud_drillcode) {
			td.classList.add('drill-active');
			textvalue = `<span data_drillcode=${value[0][3].nccloud_drillcode}>${textvalue}</span>`;
		}
	}
	colWidths.forEach((item, index) => {
		if (item == '0.01' && index == col) {
			td.style.border = '0';
			td.style.visibility = 'none';
		}
	});
	if (row == this.coords.row && col == this.coords.col && this.cellPerch == true) {
		td.style.backgroundColor = 'rgba(196,242,204,1)';
		td.style.color = 'rgba(17,17,17,1)';
	}
	td.innerHTML = textvalue;
	return td
}
