import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCModal, NCButton, NCSelect, NCFormControl, NCRadio } = base;
const NCOption = NCSelect.NCOption;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class FilterModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			operValues: {
				logicOper: '0',
				firstRelationOper: '',
				secondRelationOper: '',
				firstValue: '',
				secondValue: ''
			},
			filterValues: [],
			showModal: false,
			filterType: null, //过滤器类型
			selectData: [], //筛选字符型下拉数据
			fieldName: ''
		};
	}
	// 筛选条件变化
	handleSelect1Change(value) {
		this.setState({
			operValues: {
				...this.state.operValues,
				firstRelationOper: value
			}
		});
	}
	handleSelect2Change(value) {
		this.setState({
			operValues: {
				...this.state.operValues,
				secondRelationOper: value
			}
		});
	}
	handleInput1Change(value) {
		this.setState({
			operValues: {
				...this.state.operValues,
				firstValue: value
			}
		});
	}
	handleInput2Change(value) {
		this.setState({
			operValues: {
				...this.state.operValues,
				secondValue: value
			}
		});
	}
	// 选择 或还是与
	handleRadioChange(value) {
		this.setState({
			operValues: {
				...this.state.operValues,
				logicOper: value
			}
		});
	}
	// 字符型多值筛选change事件
	handleMulSelectChange(value) {
		this.setState({
			filterValues: value
		});
	}
	render() {
		const { handleSubmmit, handleClose } = this.props;
		let { filterType, selectData, fieldName } = this.state;
		const Children = [];
		if (filterType === null) return null;
		for (let i = 0; i < selectData.length; i++) {
			Children.push(<NCOption key={selectData[i]}>{selectData[i]}</NCOption>);
		}

		return (
			<NCModal show={this.state.showModal} id="filterModal" className="report-modal-class">
				<NCModal.Header>
					<NCModal.Title fieldid={fieldName}>
						{fieldName} {langCheck('reportMultiLang', '100301-000033')}
						{/* 国际化处理： 筛选*/}
					</NCModal.Title>
				</NCModal.Header>

				<NCModal.Body>
					{filterType && filterType === 'NUMERIC' ? (
						<div className="filter-num">
							<NCSelect
								showSearch
								style={{ width: 200, zIndex: 10000 }}
								placeholder={langCheck('reportMultiLang', '100301-000049')} /* 国际化处理： 选择操作符*/
								optionFilterProp="children"
								getPopupContainer={() => document.getElementById('filterModal')}
								onChange={this.handleSelect1Change.bind(this)}
							>
								<NCOption value="0">{langCheck('reportMultiLang', '100301-000050')}</NCOption>
								{/* 国际化处理： 等于*/}
								<NCOption value="2">{langCheck('reportMultiLang', '100301-000051')}</NCOption>
								{/* 国际化处理： 不等于*/}
								<NCOption value="4">{langCheck('reportMultiLang', '100301-000052')}</NCOption>
								{/* 国际化处理： 大于*/}
								<NCOption value="6">{langCheck('reportMultiLang', '100301-000053')}</NCOption>
								{/* 国际化处理： 大于等于*/}
								<NCOption value="5">{langCheck('reportMultiLang', '100301-000054')}</NCOption>
								{/* 国际化处理： 小于*/}
								<NCOption value="7">{langCheck('reportMultiLang', '100301-000055')}</NCOption>
								{/* 国际化处理： 小于等于*/}
							</NCSelect>
							<NCFormControl
								className="modal-input"
								type="number"
								value={this.state.operValues.firstValue}
								onChange={this.handleInput1Change.bind(this)}
							/>
							<NCRadio.NCRadioGroup
								class="model-NCRadio"
								name="fruit"
								selectedValue={this.state.operValues.logicOper}
								onChange={this.handleRadioChange.bind(this)}
							>
								<NCRadio value="0">{langCheck('reportMultiLang', '100301-000056')}</NCRadio>
								{/* 国际化处理： 与*/}
								<NCRadio value="1">{langCheck('reportMultiLang', '100301-000057')}</NCRadio>
								{/* 国际化处理： 或*/}
							</NCRadio.NCRadioGroup>
							<NCSelect
								showSearch
								style={{ width: 200, zIndex: 10000 }}
								placeholder={langCheck('reportMultiLang', '100301-000049')} /* 国际化处理： 选择操作符*/
								optionFilterProp="children"
								getPopupContainer={() => document.getElementById('filterModal')}
								onChange={this.handleSelect2Change.bind(this)}
							>
								<NCOption value="0">{langCheck('reportMultiLang', '100301-000050')}</NCOption>
								{/* 国际化处理： 等于*/}
								<NCOption value="2">{langCheck('reportMultiLang', '100301-000051')}</NCOption>
								{/* 国际化处理： 不等于*/}
								<NCOption value="4">{langCheck('reportMultiLang', '100301-000052')}</NCOption>
								{/* 国际化处理： 大于*/}
								<NCOption value="6">{langCheck('reportMultiLang', '100301-000053')}</NCOption>
								{/* 国际化处理： 大于等于*/}
								<NCOption value="5">{langCheck('reportMultiLang', '100301-000054')}</NCOption>
								{/* 国际化处理： 小于*/}
								<NCOption value="7">{langCheck('reportMultiLang', '100301-000055')}</NCOption>
								{/* 国际化处理： 小于等于*/}
							</NCSelect>
							<NCFormControl
								className="modal-input"
								type="number"
								value={this.state.operValues.secondValue}
								onChange={this.handleInput2Change.bind(this)}
							/>
						</div>
					) : filterType && filterType === 'STRING' ? (
						<NCSelect
							multiple
							style={{ width: '100%' }}
							searchPlaceholder={langCheck('reportMultiLang', '100301-000058')} /* 国际化处理： 标签模式*/
							getPopupContainer={() => document.getElementById('filterModal')}
							onChange={this.handleMulSelectChange.bind(this)}
						>
							{Children}
						</NCSelect>
					) : null}
				</NCModal.Body>

				<NCModal.Footer>
					<NCButton colors="primary" className="sure-button" onClick={handleSubmmit}>
						{langCheck('reportMultiLang', '100301-000059')}
						{/* 国际化处理： 确定*/}
					</NCButton>
					<NCButton className="cancel-button" onClick={handleClose}>
						{langCheck('reportMultiLang', '100301-000048')}
						{/* 国际化处理： 取消*/}
					</NCButton>
				</NCModal.Footer>
			</NCModal>
		);
	}
}
