import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { base } from 'nc-lightapp-front';
const { NCPagination, NCInputNumber } = base;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class Pagintion extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	render() {
		let { pager, preloadMaxRow, isClickShowAllCount, dataRowNum, isConnection } = this.props;
		let allCount;
		(isClickShowAllCount && preloadMaxRow != -1 && preloadMaxRow <= pager.allRowCount) ||
			(isConnection && preloadMaxRow != -1 && preloadMaxRow <= pager.allRowCount)
			? (allCount = preloadMaxRow + '+')
			: (allCount = pager.allRowCount);
		if (dataRowNum == 0) allCount = 0;
		return (
			<div className="pagintion" fieldid="pagination-div">
				<div className="page-left">
					<div style={{ display: 'flex', alignItems: 'center' }}>
						<NCInputNumber
							fieldid="page-size"
							value={String(pager.pageSize)}
							step={10}
							max={9999999999999}
							min={0}
							style={{ width: 70 }}
							onChange={this.props.handleChange}
						/>
						<span className="ensure" onClick={this.props.pageSizeChange}>
							{langCheck('reportMultiLang', '100301-000059')}
							{/* 国际化处理： 确定*/}
						</span>
						<span className="count">
							{langCheck('reportMultiLang', '100301-000006')} <span>{allCount}</span>{' '}
							{langCheck('reportMultiLang', '100301-000004')}
						</span>
						{/* 国际化处理： 共,条*/}
						{isClickShowAllCount &&
							preloadMaxRow != -1 &&
							preloadMaxRow <= pager.allRowCount && (
								<span className="ensure" onClick={this.props.getRealCount}>
									{langCheck('reportMultiLang', '100301-000066')}
									{/* 国际化处理： 显示合计*/}
								</span>
							)}
					</div>
					<div style={{ marginLeft: 30 }}>
						<span>
							{langCheck('reportMultiLang', '100301-000067')}
							<span className="totle" id="totalSumNum" />
						</span>
						{/* 国际化处理： 求和:*/}
						<span style={{ marginLeft: '30px' }}>
							{langCheck('reportMultiLang', '100301-000068')}
							<span className="totle" id="totalCountNum" />
						</span>
						{/* 国际化处理： 计数:*/}

					</div>
				</div>
				<div className="page-right">
					<NCPagination
						prev
						next
						boundaryLinks
						size="sm"
						gap={true}
						items={Number(pager.pageCount) || 1}
						maxButtons={5}
						activePage={pager.pageIndex ? Number(pager.pageIndex) : 1}
						onSelect={this.props.pageClick.bind(this)}
					/>
				</div>
			</div>
		);
	}
}
