import React, { PureComponent } from 'react';
import Handsontable from '../../../public/office';
import { blockFun } from './block';
import { base, ajax, toast, getMultiLang } from 'nc-lightapp-front';
import Utils from '../../../public/utils';
const { langCheck } = Utils;
require('./index.less');

const minRowLen = 25; //最小行数控制
const minColLen = 12; //最小列数控制

class SimpleTable extends PureComponent {
	constructor(props) {
		super(props);
		const _this = this;
		this.state = {
			area: [], //表格数据拥有的范围
			result: null, //表格数据
			index: '', //选中行索引
			settings: {
				copyPaste: {
					columnsLimit: 3000,
					rowsLimit: 3000
				},
				data: [], // 表格数据
				width: '100%',
				//height: 623,
				stretchH: 'all', // 排列方式
				rowHeaders: true,
				colHeaders: true,
				manualColumnResize: true,
				manualRowResize: true,
				mergeCells: [], // 合并表格
				//minSpareCols: 1, //最后永远多一列空白列
				readOnly: true,
				rowHeights: [], //单元格高度数组
				colWidths: [], //单元格宽度数组
				fixedRowsTop: 0,
				fixedColumnsLeft: 0,
				cells: function (row, col, prop) {
					this.renderer = myRender.bind(_this);
				}
			}
		};
	}

	componentWillMount() {
		let callback = (json) => {
			window.tableMultiLang = json;
		};
		getMultiLang({ moduleId: 100302, currentLocale: 'zh-CN', domainName: 'lappreportrt', callback });
	}

	componentWillReceiveProps(props) {
		const { data } = props;
		let result = data && data.data;
		let index = 0;
		if (result && result.checkTableHeadNum) {
			index = result.checkTableHeadNum;
		}
		if (result && result.cells) {
			let freezing = result.freezing;
			let fixedRowsTop = 0,
				fixedColumnsLeft = 0;
			if (freezing && freezing.isFreeze) {
				fixedRowsTop = freezing.row;
				fixedColumnsLeft = freezing.col;
			}
			this.setState({
				result,
				pk_report: result.pk_report || '',
				settings: {
					...this.state.settings,
					data: initDefaultData(result.cells),
					colWidths: coverseWidth(result.widths, result.cells && result.cells[index]),
					rowHeights: coverseHeight(result.heights, result.cells),
					mergeCells: areaCells(result.mergeInfo) || [],
					fixedRowsTop,
					fixedColumnsLeft
				}
			});
		}
	}

	afterSelection(obj, r, c, r2, c2) {
		// this.setState({
		// 	index: r
		// });
	}
	// 业务组调用获取行列
	getReportInfo() {
		const { result } = this.state;
		let reportInfo = this.refs.hansontable.returnData();
		reportInfo.coords = this.coords;
		if (result.widths && result.widths.length > 0 && result.widths[0].colname && reportInfo.colWidths.length > 0) {
			let widthArray = [];
			reportInfo.colWidths.forEach((item, index) => {
				if (reportInfo.colWidths[index].colwidth) {
					widthArray.push(reportInfo.colWidths[index].colwidth.toString());
				} else if (reportInfo.colWidths[index] == undefined) {
					widthArray.push('120');
				} else {
					widthArray.push(item);
				}
			});

			widthArray.forEach((item, index) => {
				reportInfo.colWidths[index] = {
					colname: result.widths[index].colname,
					colwidth: widthArray[index].toString()
				};
			});
		}
		return reportInfo;
	}

	getRowRecord() {
		const { index, settings } = this.state;
		return settings.data[index];
	}

	resetWidths = () => {
		const { result } = this.state;
		const width = this.getReportInfo().colWidths;
		let index = 0;
		if (result && result.checkTableHeadNum) {
			index = result.checkTableHeadNum;
		}
		this.setState({
			settings: {
				...this.state.settings,
				colWidths: coverseWidth(width, result.cells && result.cells[index])
			}
		})
	}

	resizeWidths = () => {
		const { pk_report } = this.state;
		const width = this.getReportInfo().colWidths;
		let data = {
			pk_report,
			isBusinessSet: 'true',
			width
		};
		ajax({
			url: '/nccloud/report/widget/lightreport_savewidth.do',
			data,
			success: (res) => {
				if (res.success) {
					this.resetWidths()
					toast({ content: langCheck('tableMultiLang', '100302-000002'), color: 'success' }); /* 国际化处理： 保存成功*/
				}
			},
			error: (err) => {
				this.resetWidths()
			}
		});
	};

	/***
	 * way: 冻结还是取消冻结
	 * type: 冻结的方式
	 * obj: Handsontable的ref引用
	 */
	freezeFun(way, type) {
		let that = this;
		if (way === 'block') {
			blockFun(that, type);
		} else {
			this.setState({
				settings: {
					...this.state.settings,
					fixedRowsTop: 0,
					fixedColumnsLeft: 0,
					mergeCells: areaCells(this.state.result.mergeInfo)
				}
			});
		}
	}

	afterOnCellMouseDown = (e, coords, td) => {
		let tdSelected = this.refs.hansontable.refs.hot.hotInstance.getSelected();
		this.coords = coords;
		this.tdSelected = tdSelected;
		this.state.index = coords.row
		if (this.props.onCellMouseDown) {
			this.props.onCellMouseDown(e, coords, td);
		}
	};

	render() {
		const { settings } = this.state;
		return (
			<div fieldid="report-area" style={{ width: '100%', height: '100%', minHeight: 100 }}>
				<Handsontable
					ref="hansontable"
					afterOnCellMouseDown={this.afterOnCellMouseDown}
					afterSelection={this.afterSelection.bind(this)}
					settings={settings}
				/>
			</div>
		);
	}
}

function myRender(nstance, td, row, col, prop, value, cellProperties) {
	let textvalue;
	if (value === null || !value.title || typeof value.title == 'undefined') {
		textvalue = '';
	} else {
		textvalue = value.title;
	}
	td.innerHTML = textvalue;
	td.title = textvalue.replace(/&nbsp;/ig, ' ');
	td.style.overflow = 'hidden';
	td.style.whiteSpace = 'nowrap';
	td.style.textOverflow = 'ellipsis';

	if (value !== null) {
		td.style.fontSize = '12px';
		if (value.align) {
			td.style.textAlign = value.align;
			td.style.verticalAlign = 'middle';
		}
		if (value.style == 'head') {
			td.style.backgroundColor = 'rgb(216, 230, 244)';
			td.style.color = 'rgb(21, 66, 139)';
		} else if (value.style == 'title') {
			td.style.backgroundColor = 'rgb(255, 255, 255)';
			td.style.color = 'rgb(21, 66, 139)';
			td.style.fontSize = '20px';
			td.style.height = '36px';
		} else if (value.style == 'sum') {
			td.style.backgroundColor = 'rgb(255, 248, 178)';
			td.style.color = 'rgb(0, 0, 0)';
		} else if (value.style == 'glRowColor') {
			td.style.backgroundColor = value.bgColor ? `${value.bgColor}` : 'rgb(255, 255, 255)';
			td.style.color = value.color ? `${value.color}` : 'rgb(0, 0, 0)';
		} else {
			td.style.backgroungColor = 'rgb(255, 255, 255)';
			td.style.color = 'rgb(0, 0, 0)';
		}
	}
}

function coverseWidth(widths, data) {
	let titleWidth = [];
	data.forEach((item, index) => {
		if (item === null) {
			data.splice(index, 1);
		}
	});
	if (widths && widths.length > 0 && widths[0].colname) {
		data.forEach((item, index) => {
			widths.forEach((ele) => {
				if (item.key == ele.colname) {
					titleWidth[index] = ele.colwidth;
				} else if (!item.key) {
					titleWidth[index] = '120';
				}
			});
		});
	} else if (!widths || widths.length < 1) {
		for (let i = 0; i < data.length + 1; i++) {
			titleWidth[i] = '120';
		}
	} else if ((widths && widths.length > 0 && typeof widths[0] == 'string') || typeof widths[0] == 'number') {
		return widths;
	}
	return titleWidth;
}

function coverseHeight(data, cells) {
	let titleHeight = [];
	if (!data || JSON.stringify(data) == '{}' || data.length < 1) {
		for (let i = 0; i < cells.length; i++) {
			titleHeight[i] = 23;
		}
		return titleHeight;
	} else {
		return data;
	}
}

function initDefaultData(data) {
	if (data.length < minRowLen) {
		data.length = minRowLen;
	}
	// if (data[0].length < minColLen) {
	// 	data[0].length = minColLen;
	// }
	return data;
}

function areaCells(result) {
	//和并列数据提取
	let AREAARR = [];
	result &&
		result.forEach((items) => {
			let areaObj = {
				row: items[0],
				col: items[1],
				rowspan: items[2] - items[0] + 1,
				colspan: items[3] - items[1] + 1
			};
			AREAARR.push(areaObj);
		});

	return AREAARR;
}
export default SimpleTable;
